    .status {
        height: 34px;
        width: 0px;
        position: absolute;
        bottom: 5px;
        background-color: @color1;

        .ltr &{
            right: 5px;
        }
        .rtl &{
            left: 5px;
        }
        overflow: hidden;
        -webkit-transition : all 300ms ease-out;
           -moz-transition : all 300ms ease-out;
             -o-transition : all 300ms ease-out;
                transition : all 300ms ease-out;

        .status-icon {
            width: 32px;
            height: 32px;
            position: absolute;
            top: 0px;
            right: 0px;
            background: transparent url(../UI/images/icons/icon-tickcross.png) no-repeat;
            background-size: 300% 600%;
            background-position: 100% 0;
        }
    }
    .submodulesWrap{
        
        
        @media(min-width: @deviceWidthLG){
            overflow: hidden;
            height: 100%;
            margin-left: -10px;
        }
    }
    .submodules{
        padding: 10px;
        @media(min-width: 768px){
            columns: 2;
        }
        @media(max-width: @deviceWidthMaxMD){
            &:not(.shown){
                display: none;
            }
        }
        @media(min-width: @deviceWidthLG){
            height: 100%;
            .transition(.5s);
            &:not(.shown){
                .translate(-100%,0);
            }
        }

        .submodule-btn{
            display: block;
            text-decoration: none;
            color: @white !important;
            .submodule-title{
                padding: 5px;
            }
            border-bottom: 1px solid @color1Light;
            font-weight: bold;
            
            position: relative; 
            -webkit-column-break-inside: avoid;
                      page-break-inside: avoid;
                           break-inside: avoid;
            .status {
                width: 0;
                height: 22px;
                right: 0;
                .status-icon {
                    width: 100%;
                    height: 100%;
                }
            }

            &:not(.disabled){
                cursor: pointer;
                &:hover{
                    color: black;
                    background-color: White;
                    background: white;
                    // .applyModuleStyling(color);
                }
            }

            &.done{
                .submodule-title{
                    .ltr &{
                        padding-right: 25px;
                    }
                    .rtl &{
                        padding-left: 25px;
                    }
                }
                .status{
                    width: 22px;
                }
            }
        }
    }
    .inactive-image{
        display:none;
    }
//        height: 100px;
.locked{
    .active-image{
        display:none;
    }
    .inactive-image{
        display:block;
    }
    
}
