.component-separator{
    height: 100px;
    &.small{
        height: 50px;
    }
    &.large{
        height: 150px;
    }
    &.height30{
        height: 0;
        padding-bottom: 30%;
    }
    &.height40{
        height: 0;
        padding-bottom: 40%;
    }
    &.height50{
        height: 0;
        padding-bottom: 50%;
    }
}