// main: styles.less

// logo
@logoMargin: 12px;
@maxLogoWidth: 91px;
@maxLogoHeight: 25px;
@maxLogoWidthMobile: 91px;
@maxLogoHeightMobile: 25px;

@navbarHeight: 60px;
@footerHeight: 0px;
@viewportVisibleHeight: calc(100vh - @navbarHeight);

// Colour definitions
@white: #fff;
@black: #000;

//@greyDarker: #505050;
@greyDark: #404040;
@greyMid: #d7d8d6;
@grey: #d7d8d6;
@greyLight: #ededed;


// Brand Colour definitions (HSBC)
@hsbcRed: #DB0011;
@hsbcRed1: #e31e22;
@hsbcRed2: #ba1110;
@hsbcRed3: #730014;
@slate: #3e505d;
@hsbcBlack: #232322;
@hsbcYellow: #edb735;
@hsbcBlue: #3d8eae;
@hsbcGreen: #3e701a;
@hsbcLightGreen: #deead6;
@hsbcDarkGreen: #176a47;

@colorHsbcRed: @hsbcRed1;

@correctGreen: #4C7C27; // HSBC Green #2
@incorrectRed: @hsbcRed;

//HSBC branding - primary colours
@primary1: @hsbcRed;
// @primary2: #f00;
// @primary3: #f00;
// @primary4: #f00;
// @primary5: #f00;

//secondary colours
@secondary1: #b7be1c; // emphasis green
// @secondary2: #b7be1c;

@colorWhite: @white;
@colorBlack: @black;
@colorGrey: @grey;
@colorLightGrey: @greyLight;
@colorDarkGrey: @greyDark;

// light and dark colours
@colorLight: @white;
@colorDark: @black;

// create variables for the primary colours
@color1: @primary1;
@color1Light: lighten(@primary1, 10%);
@color1Dark: darken(@primary1, 10%);
@color1Tint85: tint(@primary1,15%);
@color1Tint70: tint(@primary1,30%);

