// @color5: @primary5;
// @color5Light: lighten(@color5, 10%);
// @color5Dark: darken(@color5, 10%);

// create variables for the secondary colours
@colorSecondary1: @secondary1;
@colorSecondary1Light: lighten(@colorSecondary1, 10%);
@colorSecondary1Dark: darken(@colorSecondary1, 5%);
