// charts
@chartColor1: #ed6335;
@chartColor2: #5d9438;
@chartColor3: #3d8eae;
@chartColor4: #f14e73;
@chartColor5: #943888;
@chartColor6: #edb735;
@chartColor7: #ba1110;

// generate color classes for common base colours
@basecolors: {
    White: @colorWhite;
    Black: @hsbcBlack;
    HsbcRed: @primary1;
    // MidGrey: @colorMidGrey;
    LightGrey: @colorLightGrey;
    DarkGrey: @colorDarkGrey;
    Light: @colorLight;
    Dark: @colorDark;
    
    1: @color1;
    //2: @color2;
    //3: @color3;
    //4: @color4;
    //5: @color5;
    Secondary1: @colorSecondary1;
}

// loop through object above to generate color classes
each(@basecolors, {
    .generateColors(@key);
});


// add colours to this list to be able to use as class e.g. .blue will use @blue
@namecolors: {
    hsbcRed: hsbcRed;
}  
// loop through object above to generate color classes and apply module class styling
each(@namecolors, {
    .generateColorsByName(@key);

    .moduleColorClassStyling(@key, @value, @white); // set secondary to white by default
});
//.moduleColorStyling(@key, @white); // override secondary to custom if required

// apply grey styling where .grey class is used within #content
#content .grey{
    .moduleStyling('grey', @white);
}

// create module specific styles
.moduleStyling('hsbcRed', @white); //default module styling

