// main: ../../less/styles.less

.component-flipcard{
    /* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
    @flipcard-icons-size: 38px;
    @flipcard-icon-x-offset: 2px;
    @flipcard-icon-y-offset: 2px;
    .flip-card {
        background-color: transparent;
        width: 100%;
        min-height: 150px;
        margin: 0 auto;
        perspective: 1000px;
        cursor: pointer;

        
        &:hover,
        &:focus{
            .flip-icon{
                background-color: @buttonBkgColor;
            }
        }

        &:focus{
            .btnFocus;
        }
        /* Remove this if you don't want the 3D effect */
        /* This container is needed to position the front and back side */
        .flip-card-inner {
            position: relative;
            width: 100%;
            height: 100%;
            html.preserve3d &{
                .transition(.8s);
//                transition: transform 0.8s;
                transform-style: preserve-3d;
            }
            html.no-preserve3d &{
                //overflow: hidden;
            }
            
            // &:not(.bkgImage){
                // .applyModuleStyling(background-color);
            // }

            /* Position the front and back side */
            .flip-card-front,
            .flip-card-back {
                position: absolute;
                width: 100%;
                height: 100%;
                // left:0;
                // display: table;
                overflow: hidden;
                //html.preserve3d &{
                    -webkit-backface-visibility: hidden;
                    backface-visibility: hidden;
                //}
                html.no-preserve3d &{
                    .transition(.8s);

                }
                .flip-card-content{
                    padding: 2px;
                    // display: table-cell;
                    // vertical-align: top;                    
                    display: flex;
                    // justify-content: center;
                    // align-items: center;
                    flex-direction: column;
                    height: 100%;
                    .flip-card-content-components{
                        flex: 1;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                    }
                }
                .flip-icon{
                    width: @flipcard-icons-size;
                    height: @flipcard-icons-size;
//                    display: none;
                    position: absolute;
                    bottom: @flipcard-icon-y-offset;
                    right: @flipcard-icon-x-offset;
                    background-color: @buttonBkgColor;
                    padding: 8px;
                    .transition;
                    .svgWrap{
                        stroke-width:8px;
                    }
                }
                // .flip-card-content{
                //     padding-bottom: 30px;
                // }
            }

            /* Style the front side (fallback if image is missing) */
            .flip-card-front {
                background-color: @white;
                // .applyModuleStyling(background-color);
                // color: #fff;//#57479c;
                text-align: center;
                // cursor: pointer;
                border: 1px solid @greyDark;
                .flipcard-title{
                    padding: 10px ( @flipcard-icon-x-offset + @flipcard-icons-size );
                    background-color: #000;
                    color: #fff;
                    width: 100%;
                    
                    font-size: 1.115rem;
                    // font-weight: bold;
                    // font-size: 1.2em;
                }
//                    margin-left: 20px;
//                    margin-right: 60px;
//                    font-size: 14px;
//                }
                .flip-card-content {
                    position: relative;
                    width: 100%;
                    .component-image{
                        max-width: 50%;
                        margin: 0 auto;
                    }
                    /*.component-image{
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        // max-width: 100px;
                        margin: 0 auto;
                        width: 100%;
                        max-width: 100%;
                        // max-width: 350px;
                        .svgWrap{
                            stroke: @white;
                        }
                    }*/
                }
            }

            /* Style the back side */
            .flip-card-back {
                background-color: @greyLight;
                padding: 10px;
                // text-align: center;
                .bkgColorLightGrey &{
                    background-color: @white;
                }
                .feedback &,
                .component-tab &{
                    background-color: @white;
                }
                .text-center& {
                    text-align: center;
                }
                .text-back-top&{
                    .flip-card-content{
                        vertical-align: top;
                    }
                }
                .text-back-center&{
                    .flip-card-content{
                        vertical-align: middle;
                    }
                }
                html.preserve3d &{
                    .transform(rotateY(180deg));
                }
                html.no-preserve3d &{
                    .transform(rotateY(-180deg));
                    //display: none;
                    //left:100%;
                    //opacity: 0;
                }
                .flipcard-title{
                    // font-size: 1.2em;
                    .applyModuleStyling(color);
                    // font-weight: bold;
                    .no-back-title&{
                        display: none;
                    }
                }
                .flipcard-text{
                    h3 {
                        .h2;
                    }
                }
                .flip-icon{
                    background-color: @slate;
                }
            }
            
            &:not(.bkgImage){
                .flip-card-front {
                    .flip-card-content {
                        .transition();
                        .applyModuleStyling(background-color);
                        &:hover{
                            .applyModuleStyling(background-color);                        
                        }
                    }
                }
            }
            &.bkgImage{
                .flip-card-front {
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: center;
                    // .flipcard-img-container{
                        // display: flex;
                        // justify-content: center;
                        // align-items: center;
                        // .flipcard-img{
                        //     width: 100%;
                        //     height: 100%;
                        //     max-width: 210px;
                        //     max-height: 220px;
                        //     background-size: contain;
                        //     // background-size: cover;
                        //     background-repeat: no-repeat;
                        //     background-position: center;
                        // }
                    // }
                    .flipcard-title{
                        // .applyModuleStyling(background-color);
                        // display: block;
                        // padding: 10px;
                        // // .flipcard-title-inline{
                            // display: inline-block;
                            // height: 100%;
                            // padding: 10px ( @flipcard-icon-x-offset + @flipcard-icons-size );
                            // background-color: #000;
                            // color: #fff;
                            // width: 100%;
                        // }
                    }
                    .flip-card-content {
                        // padding: 0px;
                        // vertical-align: bottom;
                        justify-content: flex-end;
                        .transition();
                        background-color: transparent;
                        &:hover{
                            .applyModuleStyling(background-color, '', 50%);                        
                        }
                    }
                    // .flip-icon{
                    //     width: 34px;
                    //     height: 34px;
                    //     top: 0;
                    //     right: 0;
                    //     padding: 2px;
                    //     // .applyModuleStyling(background-color);
                    // }
                }
            }
        }
        &.flip{
            .flip-card-inner {
                html.preserve3d &{
                    .transform(rotateY(180deg));
                }
                html.no-preserve3d &{
                    .flip-card-front {
                        .transform(rotateY(180deg));
                        //display: none;
//                        left:-100%;
                    }
                    .flip-card-back {
//                        .transition-delay(.4s);
                        .transform(rotateY(0deg));
                        //display: table;
//                        left:0%;
                        opacity: 1;
                    }
                }
            }
        }
    }
    
    .test-flipcard {
        position: relative;
        width: 220px;
        height: 160px;
        perspective: 500px;
    
        &.v{
            &.flip{
                .front{
                    transform: rotateX(180deg);
                }
                .back{
                    transform: rotateX(0deg);
                }
            }
            .back{
                transform: rotateX(-180deg);
            }
        }
        &.h{
            &.flip{
                .front{
                    transform: rotateY(180deg);
                }
                .back{
                    transform: rotateY(0deg);
                }
            }
            .back{
                transform: rotateY(-180deg);
            }
        }   
//    .test-flipcard.v:hover .back, .test-flipcard.v.flip .back{
//      transform: rotateX(0deg);
//    }
//    .test-flipcard.v .back{
//      transform: rotateX(-180deg);
//    }
//    .test-flipcard.h:hover .front, .test-flipcard.h.flip .front{
//      transform: rotateY(180deg);
//    }
//    .test-flipcard.h:hover .back, .test-flipcard.h.flip .back{
//      transform: rotateY(0deg);
//    }
//    .test-flipcard.h .back{
//      transform: rotateY(-180deg);
//    }
        .front, 
        .back{
          position:absolute;
          width: 100%;
          height: 100%;
//          box-sizing: border-box;
          transition: all 0.5s ease-in;
          color: white;
          background-color: #000;
          padding: 10px;
        }
        .back{
          backface-visibility: hidden;
        }
    }
    
    &.principles{
        .flip-card-inner{
            .flip-card-back {
                .flip-card-content{
                    vertical-align: top;
                    .flipcard-text{
                        display: inline-block;
                        text-align: left;
                        ul {
                            padding-left: 0;
                            > li{
                                list-style: none;
                            }
                        }
                    }
                }
            }
            .irrelevent{
                opacity: .3;
            }
        }
    }
}