    &.accordion{
        width: 100%;
        text-align: left;
    }
    
    &.startBtn{
        font-size: 2em;
        min-width: 200px;
        min-height: 50px;
        float: right;
        &:after{
            content:'';
            background: url('../UI/images/modulenav/Arrow_wht_backnext.png');
            background-size: 37px 40px;
            width: 19px;
            height: 40px;
            background-position: -100% 0;
            position: absolute;
            right: 5px;
            top: 50%;
            margin-top: -20px;
            .transition();
        }
        &:hover{
            &:after{
                right: 0;
            }
        }
    }
    
