    &.arrow-btn{
        padding-right: 30px;
         &:after{
            content:'';
            background: url('../UI/images/arrow_wht.png');
            width: 21px;
            height: 21px;
            position: absolute;
            right: 5px;
            top: 50%;
            margin-top: -10px;
            .transition();
        }
        &:hover{
            &:after{
                right: 0;
            }
        }
    }
    
    &.btn-submenu{
        overflow: hidden;
        .status{
            position: absolute;
            right: -24px;
            top: 0;
            width: 24px;
            height: 24px;
            padding: 1px;
            background-color: fade(#fff, 70%);
            .applyModuleStyling(background-color);
            .transition();
            .svgWrap{
                stroke-width: 6px;
            }
        }
        &.done{
            .status{
                right: 0px;
            }
        }
    }
    
    .btnSubtext {
        font-weight: normal;
        font-size: 0.8em;
    }

    .btn-link-text {   
        display: block;
        font-size: .8em;
        font-style: italic;
    }

    // &.hotspot{
    //     background-color: transparent;
    // }
    &.hotspot {
        &.done {
            .svg-original {
                display: none;
            }
            .svg-complete{
                display: block;
            }
            background-color: @greyMid !important;
            color: black !important;
        }
        &:not(.done){
            .svg-original {
                display: block;
            }
            .svg-complete{
                display: none;
            }
        }
    }
}