// main: ../../less/styles.less

.component-header{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 0;
    .header-container{
        position: relative;
        padding: 25vh 20px;
        .component-parallaxheader &{
            padding: 0;
        }
        .header-btn{
            position: absolute;
            width: 40px;
            
            min-width: auto;
            min-height: auto;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            background-color: @primary1;
            cursor: pointer;
            padding: 15px 10px;
            &:focus{
                .btnFocus;
            }
            .svgWrap{
                stroke-width: 8px;
            }
            .component-parallaxheader &{
                display: none;
            }
        }
        .header-heading-container{
            display: inline-block;
            *{
                display: inline-block;
            }
            h1{
                @media (max-width: @deviceWidthSM){
                    font-size: 1.5rem;
                }
            }
            p{
                margin-top: 1em;
                font-size:1.3rem;
                font-style: italic;
                @media (max-width: @deviceWidthSM){
                    font-size: 1rem;
                }

            }
            // @media (max-width: @maxXS){
            //     text-align: center;
            // } 
        }
    }
}
