.component-buildupcarousel{
    &:not(.pager-top){
        padding-bottom: 40px;
    }

    .carousel-slide{
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
    
        // padding: 2em;
        // min-height: 50vh;
        
        

        .story-bkg{
            background-size: cover;
            background-position: 50% 50%;
            width: 100%;
            
            &:before{
                content: '';
                display: block;
                padding-bottom: 56.25%;
                .ratio4x1&{
                    padding-bottom: 25%;
                }
                .ratio16x6&{
                    padding-bottom: 37.5%;
                }
            }
        }
    }

    &:not(.text-bottom, .text-top){
        .carousel-slide{
            position: relative;
            // padding: 2em;
            // min-height: 50vh;
            
    
            @media (min-width: @minMD) {
                align-items: end;
                padding-left: 60px;
                padding-right: 60px;
                
                &:before{
                    content: '';
                    display: block;
                    padding-bottom: 56.25%;
                    .ratio4x1&{
                        padding-bottom: 25%;
                    }
                    .ratio16x6&{
                        padding-bottom: 37.5%;
                    }
                }
            }
            .story-bkg{
                @media (min-width: @minMD) {
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                }
            }
            .story-text{
        
                @media (min-width: @minMD) {
                    margin: 0 auto 1em;
                }
            }
        }
    }

    &.text-top{
        .carousel-slide{
            flex-direction: column-reverse;
        }
    }
    &.text-top, &.text-bottom{
        .carousel-slide{
            gap: 1em;
        }
    }
    &.controls-outside{        
        .carousel-slide{
            padding-left: 60px;
            padding-right: 60px;
        }
    }
    .story-text{
        position: relative;
        // width: 100%;
        padding: 20px;
        // margin: 0 60px 1em;

        .no-bkg&{
            display: inline-block;
            background-color: @greyDark;
            color: @white;
        }

        @media (max-width: @maxSM) {
            padding: 10px;
        }
    }

    .bx-wrapper {
        margin-bottom: 0;

        .box-shadow(0 2px 10px rgba(0,0,0,.3));
        .no-bkg&{
            .box-shadow(none);
        }
        
        .bx-viewport{
            .no-bkg&{
                background-color: transparent;
            }
            background-color: @white;
        }
        .bx-controls-direction a{
            &:focus{
                .btnFocus;
            }
        }
        .bx-prev, 
        .bx-next{
            // background: url(../UI/images/icons/carousel-arrows.png) no-repeat center;
            background-color: @buttonBkgColor;
            width: 50px;
            height: 50px;
            
            .box-shadow(0 0px 30px rgba(0,0,0,.3));
            .transition;
            .svg-icon{
                position: absolute;
                top: 50%;
                left: 5px;
                right: 5px;
                transform: translateY(-50%);
                .svgWrap{
                    stroke-width: 4px;
                }
            }
            &:hover,
            &:focus{        
                background-color: @buttonHoverBkgColor;
            }
            @media (max-width: @maxSM) {
                width: 40px;
                height: 40px;
                &.bx-prev{
                    left: -5px;
                }
                &.bx-next{
                    right: -5px;
                }
            }
        }
        .bx-pager{
            .pager-top&{
                padding: 10px;
                background-color: #fff;
                bottom: 0;
                width: auto;
                font-size: 0;
                border-radius: 16px;
                left: 50%;
                transform: translateX(-50%);      
            }
        }
    }
