// main: ../../less/styles.less

.component-parallaximage{

    
    display: flex;
    // flex-direction: column;
    flex-wrap: wrap;
    // min-height: @parallaxheaderHeight;
    width: 100%;
    // overflow: hidden;
    
    // .fullscreen&{
    //     height: @viewportVisibleHeight;
    // }

    &.fullBkg{
        position: absolute;
        top: 0;
        bottom: 0;   
        left: 0;
        right: 0;
        margin-bottom: 0;
    }

    .d-flex &{
        flex: 1;
    }

    .buildup-container{
        overflow: hidden;
        
        width: 100%;
        .d-flex &{
            height: 100%;
        }
    }
    .buildup-layer-wrap{
        position: relative;
        width: 100%;
        .ltr &{
            .scale(1.1);
        }
        .rtl &{
            .scale(-1.1, 1.1);
        }
        // .fullscreen&{
        // }
        // .d-flex &{
            // max-width: none;
            // max-height: 100%;
            height: 100%;
        // }
        
        .buildup-layer-temp-image{
            position: relative;
            visibility: hidden;
            max-width: 100%;
            .d-flex &{
                // max-width: none;
                // max-height: 100%;
                display: none;
            }
        }

        .buildup-layer{
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            .img-top&{
                background-position: top center;
            }
            .img-bottom&{
                background-position: bottom center;
            }
        }
    }
    
    // align-items: flex-end;
    // .section{
        
    // }
    
    // .applyModuleStyling(background-color);

    &.hasImage{
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        // @media(min-width: 768px){
        //     height: 40vh;
        // }
        
        // &:not(.fullscreen){
        //     align-items: flex-end;
        // }
    }
    
    
    
    // margin-bottom: 0 !important;

    .overlay{
        width: 100%;
        &.title-overlay{
            position: absolute;
            z-index: 10000;
            // align-self: start;
            .overlayContent{
                display: inline-block;
                padding: 10px 20px;
                margin: 0 -10px;
                color: #fff;
                .applyModuleStyling(background-color);
                // .fullscreen&{
                //     // height: @viewportVisibleHeight;
                //     margin-top: 15vh;
                // }
            }
        }
        &.content-overlay{
            margin-top: -10vh;
            animation-delay: 4s;
            align-self: end;
            .overlayContent{
                display: inline-block;
                padding: 20px;
                margin: 0 -10px;
                // color: #fff;
                // .applyModuleStyling(background-color);
                // background-color: fade(lighten(#0460A9, 60%), 90%);
                background-color: #fff;
                // .fullscreen&{
                //     margin-bottom: 15vh;                    
                // }
            }
        }
    }

}