// main: ../../less/styles.less

.component-image{
    background-repeat: no-repeat;
    &.center{        
        img{
            margin: 0 auto;
        }
    }
    .img-label {
        position: relative;
        padding: 10px 2px;
        background-color: #fff;
        text-align: center;
        @media(min-width: 768px){
            margin-top: -30px;
            margin-left: 20px;
            margin-right: 20px;
        }
    }
    &.label-dark{
        .img-label{
            background-color: @hsbcBlack;
            color: #fff;
        }
    }
    .hotspots{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        .hotspotText{
            width: auto !important;
            display: flex;
            .btnText{
                padding-right: 10px;
                margin-top: -4px;
            }
            .svgWrap{
                width: 21px;
                margin-top: -5px;
                margin-right: 10px;
                stroke-width: 6px;
            }
        }
        .hotspot{
            position: absolute;
            min-width:auto;
            width:40px;
            height:40px;
            margin-left:-20px;
            margin-top:-20px;
            border-radius: 20px;
            padding: 8px;
            border: 2px solid #fff;
            .svgWrap{
                stroke-width: 6px;
            }
           /* background-color: #fff !important;
            .applyModuleStyling(border-color);
            &:hover{
                .svgWrap{
                    stroke: #fff;
                }
            }*/
//            &.complete{
//                background-color: #fff !important;
//            }
        }
        .phonehotspot{
            position: absolute;
            left: 40%;
            top: 57%;
            width: 17%;
            height: 12%;
            background-color: transparent !important;
            &:not(.disabled){
                &:hover{
                    background-color: fade(@color1, 30%) !important;
                }
            }
        }
    }
    // .row:not(.aligned-row) &{
    //     background-image: none !important;    
    // }
//    }
