        .aligned-row &{
//            height: 100%;
            flex: 1; // make fill remaining height
            background-size: cover;
            background-position: center center;
            img {
                display: none;
            }
        }
    }

    .section:not(.overspill) .row:not(.aligned-row) &{
        background-image: none !important; 
    }*/

    // div.d-flex &{

    // }
    // background-image: none !important; 

    
    .section:not(.overspill) div:not(.d-flex) > &{
    // .section:not(.overspill) div:not(.d-flex) &{
        background-image: none !important; 
    }
    .section.overspill &{
        &:not(.overspill-image){
            background-image: none !important; 
        }
    }

    .d-flex > &{
        @media(min-width: 768px){
            flex: 1; // make fill remaining height
            background-size: cover;
            background-position: center center;
            img {
                display: none;
            }
        }
    }

    &.overspill-image{       
        .section.overspill div[class^='col-']:not(.overspill-inactive) &{            
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            background-position: center;

            @media (min-width: 768px){
                width: 50vw;
            }
            &.overspill-image-fill{
                bottom: 0;
                margin-bottom: 0 !important;                   
            }
            img{
                visibility:hidden;
            }
        }
        
    }
    
    .component-conversation &{
        max-width: 250px;
        margin: 0 auto;
        .img-label {
            .applyModuleStyling(background-color);
            color: #fff;
        }
    }

    &.image-icon{
        max-width: 100px;
        .text-center &{
            margin: 0 auto;
        }
    }
}