// main: ../../less/styles.less

.component-plusbutton{
    @plus-btn-size: 50px;  
    width: 100%;
    height: 220px + (@plus-btn-size / 2); 
    margin-bottom: @plus-btn-size / 2;

    &.text-top{
        height: auto;
    }

    &:focus{
        .btnFocus;
    }

    .plusbutton-container{
        position: relative;
        width: 100%;
        height: 100%;
        padding: 15px;
        padding-bottom: 30px;
        display: flex;
        flex-direction: row;
        align-items: end;
        margin-bottom: @plus-btn-size / 2;
        background-color: @greyLight;
        .bkgColorLightGrey &,
        .bkgColorLightGreen &{
            background-color: @white;
        }
        cursor: pointer;
        .plusbutton-img{
            position: absolute;
            // padding: 5px;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            .plusbutton-icon&,
            .contain-image&{
                object-fit: contain;
            }
            .plusbutton-icon&{
                max-height: 150px;
            }
        }
        .plusbutton-text{
            padding: 12px 25px;
            color: #fff;
            font-size: 1.115rem;
            background-color: #000;
            z-index: 2;
            .label-transparent&{
                background-color: transparent;
                padding: 0;
                .font1Light();
                color: @bodyColor;
            }
            .text-hidden&{
                opacity: 0;
            }
        }
        .plus-btn{
            position: absolute;
            width: @plus-btn-size;
            height: @plus-btn-size;
            left: 50%;
            bottom: 0;
            transform: translate(-50%, 50%);
            background-color: @buttonBkgColor;
            border-radius: 50%;
            cursor: pointer;
            z-index: 2;
            .transition;
            .plus-btn-icon-tick{
                display: none;
            }
            .complete&{
                .plus-btn-icon-plus{
                    display: none;
                }
                .plus-btn-icon-tick{
                    display: block;
                }
                background-color: @greyMid;
            }
            .plus-icon-container{
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                height: 16px;
                width: 16px;
                .svgWrap{
                    stroke-width: 8px;
                }
            }
        }
    }

    &.plusbutton-icon,
    &.label-top,
    &.label-bottom{
        height: auto;
        .plusbutton-container{
            // position: relative;
            // width: 100%;
            // height: 100%;

            padding: 5px;
            .p-0&{
                padding: 0;
            }
            // display: block;
            
            .plusbutton-img{
                position: static;
            }
            .plusbutton-text{
                padding: 15px;
                .label-transparent&{
                    padding-top: 5px;
                    padding-bottom: 5px;
                }
            }
        }
    }
    &.label-top{
        .plusbutton-container{
            display: flex;
            align-items: flex-start;
            flex-direction: column-reverse;
            .label-transparent&{
                align-items: normal;
            }
        }
    }
    &.plusbutton-icon:not(.label-top),
    &.label-bottom{
        .plusbutton-container{
            .plusbutton-text{
                padding-top: 5px;
                padding-bottom: 30px;
            }
        }
    }
    &.plusbutton-icon{
        &.label-top{
            .plusbutton-container{
                .plusbutton-img{
                    padding-bottom: 10px;
                }
            }
        }
    }
    &:not(.disabled){
        .plusbutton-container{
            &:hover,
            &:focus{
                // .plusbutton-text{
                //     background-color: @primary1;
                // }
                .plus-btn{
                    background-color: @buttonHoverBkgColor;
                }
            }
        }
    }
}