// main: styles.less
.svgWrap{
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    
    stroke: @white;
    fill: transparent;
    stroke-width: 2px;

    svg{
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
    }
    &.svgFill,.svg-fill{
        fill: @white;
        stroke-width: 0;
    }
    .svg-stroke{
        stroke: @white;
        fill: transparent;
        stroke-width: 2px;
    }
    .inline-svg &{
        stroke-width: 0.4em;
        display: block;
        .applyModuleStyling(stroke);
        &.svg-fill{
            stroke-width: 0;
            .applyModuleStyling(fill);
        }
    }
    .svgModuleColor &{
        .applyModuleStyling(stroke);
        &.svg-fill{
            .applyModuleStyling(fill);
        }
    }

    .svg-audio &{
        #crossLeft,
        #crossRight{
            opacity: 0;
        }
    }
}

.inline-svg{
    width: 1.5em;
    display: inline-block;
    vertical-align: bottom;
