.svgPreview{
    .svgPreviewItem{
        width: 100px;
        float: left;
        margin: 2px;
        position: relative;
        &.svgPreviewExternal{
            background-color: #bbb;
        }
        &.svgPreviewInternal{
            background-color: #ccc;
        }
        .svgPreviewItemSVG{
            margin: 10px;
            margin-top: 20px;
            width: 80px;
            height: 80px;
        }
        .svgPreviewItemLabel{
            position: absolute;
            width: 100%;
            top: 0;
            font-size: .8em;
            text-align: center;
        }
    }
}