// navigation
html, body{
    height: 100%;
    font-size: @bodyFontSize;
    background-color: @htmlBkgColor;
}
body{
    .font1();
   line-height: @bodyLineHeight;
	height: auto;
    min-height: 100%;
    color: @bodyColor;
    .contentDark{
        color: @bodyColor;
    }
    .contentLight{
        color: @bodyLightColor;
    }
    padding-top: 0px;
    //.transition-properties(padding-top, 1s);
    margin: 0px;
    background-color: @bodyBkgColor;
    #content-wrapper{
        position: relative;
    }
    
    .navbar.navbar-fixed-top{
        .transition(1s);
    }
    
    .navbar.navbar-fixed-top{
        top: -(@maxLogoHeight+10px);
    }
    &.responsive{
        padding-top: 0px;
    }
    
    &.nav-visible{
        .navbar.navbar-fixed-top{
            top: 0px;
        }
        &.responsive{
            padding-top: @navbarHeight;//(@maxLogoHeight+2*@logoMargin);
        }
    }
    
    //&.nav-hidden{
    //}
    
    &.debug-on{
        .navbar.navbar-fixed-top{
            top: -50px;
        }
        &.responsive{
            padding-top: 28px;
        }
        &.nav-visible{
            .navbar.navbar-fixed-top{
                top: 28px;
            }
            &.responsive{
                padding-top: 75px;
            }
        }
        //&.nav-hidden{
        //}
    }
    &.responsive{
        //padding-top: 50px;
        #content-wrapper{
            height: 100%; 
        }
    }
    &.not-responsive{
        #content-wrapper{
            width: 1000px; 
            height: 600px; 
            margin: 0 auto; 
            overflow: hidden;
        }
    }
    &.page-anim-out-fw,
    &.page-anim-in-bw{
        .pageContainer{
            //.transform(translateY(-50px));
            .opacity(0);
        }
    }
    &.page-anim-out-bw,
    &.page-anim-in-fw{
        .pageContainer{
            //.transform(translateY(50px));
            .opacity(0);
        }
    }
    &.page-anim-in-fw,
    &.page-anim-in-bw{
        .pageContainer{
            .transition-duration(0s);
        }
    }
}

