.bottomNavigation{
	body:not(.nav-bottom-active) &{
		display: none;
	}
    @media(max-width: @deviceWidthMaxXS){
        .nav-btns-container{
            padding: 0 15px;
            .nav-btns {
                padding-left: 0;
                padding-right: 0;
                flex-wrap: wrap;
                justify-content: flex-start !important;
                gap: 1em !important;
                > li{
                    flex-basis: calc(50% - 0.5em);
                    &#li_btn_Exit{
                        order: 2;
                    }
                    &#li_btn_Menu{
                        order: 4;
                    }
                    &#li_btn_Back{
                        order: 0;
                    }
                    &#li_btn_Next{
                        order: 1;
                    }
                    button{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        } 
    }
    &.fixed{
        position: fixed;
        bottom: 0px; 
        width: 100%;
    }
    background-color: @greyLight;
    color: black;
    .nav-btns{
        margin: 2em 0;
        display: flex;
        justify-content: center;
        gap: 2em;
        // .page-0 &, .page-1 &{
        //     visibility: hidden;
        // }
        > li{
            display: inline-block;
            .nav-btn{
                padding: 0.5em 1.5em;
                border: 1px solid black;
                background-color: transparent;
                color: black;
                display: block;
                text-decoration: none;
                text-align: center;
                .transition;
                &.disabled{
                    // opacity: .4;
                    color: lighten(@hsbcBlack, 40%);
                    border-color: lighten(@hsbcBlack, 40%);
                }
                &:not(.disabled){
                    &:hover,
                    &:focus{
