                        color: @hsbcBlack;
                    }
                }
            }
        }
    }
    .content-locked-alert{
        padding: 2em 0;
        color: #b8b8b8;
        text-align: center;
        
        .page-0 &, .page-1 &{
            display: none !important;
        }
        .page-anim-in-fw &,
        .page-anim-in-bw &,
        .page-anim-out-fw &,
        .page-anim-out-bw &{
            display: none !important;
        }
    }
