.scroll-arrow{
    position: absolute;
    width: 46px;
    height: 30px;
    bottom: 10px;
    background: transparent url(../UI/images/icons/scrollDownArrows.png) center no-repeat;
    background-position: 0 -30px;
    cursor: pointer;
    display: none;
    z-index: 10;
    .animation(scrollDownAnim 1s 3);
    .ltr &{
        right: -46px;
    }
    .rtl &{
        left: -46px;        
    }
    .help &{
        position: static;
        display: block;
        margin: 0 auto;
    }
}

