.modal{
    z-index: 10000;
    display: flex;
    align-items: center;
    .modal-backdrop{
        z-index:auto;
    }
    .modal-dialog {
        // position: absolute;
        // top: 0px;
        // right: 0px;
        // bottom: 0px;
        // left: 0px;
        @media (max-width: @deviceWidthSM) {
            padding-right: 15px;
            padding-left: 15px;
        }
        margin: auto !important;
        // height: 90%; //height: 400px;
        &.help{
            width: 90%;
            max-width: 1000px;
            // height: 90%;
            .helpRow{
                display:flex;
                >div{
                    margin-bottom: 10px;
                }
            }
            .helpitem{
                background-color: @greyLight;
                // margin-bottom: 10px;
                height: 100%;
                padding: 10px;
                >.table{
                    &:not(:last-child){
                        margin-bottom: 5px;
                    }
                }
                .tablecell{
                    float: none;
                    &.tableImage{
                        width: 120px;
                        text-align: center;
                    }
                }
            }
        }
        .large&{
            width: 90%;
            max-width: 1000px;
        }
        .top&{
            // height: 90%;
        }
    }
    .modal-header {
        border-bottom: none;
        // padding: 15px 15px 0px 15px;
        padding: 0 !important;
        // border-top: 6px solid;
        // .applyModuleStyling(border-top-color);
        >h2{
            margin-bottom: 1em;
        }
    }
    
    .modal-content {
        border-radius: 0px;
        box-shadow: none;
        border: none;
        padding: 60px; //48px 48px 15px 48px;
        
        .btn-close-container{
            position: absolute;
            top: 0;
            right: 0;
            width: 58px;
            min-width: 0;
            height: 58px;
            padding: 0;
            background-color: @hsbcRed;
            cursor: pointer;
            .btn-close{
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                width: 16px;
                height: 16px;
                .svgWrap{
                    stroke-width: 8px;
                }
            }
        }
        .anim{
            opacity: 1;
        }
    }
    .modal-body{
        padding: 0;
        >*{
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
//
