.sidemenu{
    position: fixed;
    top: 50%;
    margin: 0;
    padding: 0;
    list-style: none;
    display: none;
    
    // mobile
    @media (max-width: 767px){
        display: block;
        top: auto;
        bottom: -10%;
        left: 50%;
        .transform(translateX(-50%));
        .transition-properties(bottom 0.3s ease-out);
        .nav-visible &{
            bottom: 0%;
        }
    }
    
    @media (min-width: 768px){
        
        .ltr.nav-visible &{
            display: block;
            right: 0;
            .transform(rotate(90deg) translate(50%, 50%));
            .transform-origin(100% 50%);
            .btn_Contact{
                padding-right: 45px;
            }        
            .btn_icon{
                right: 5px;
                .rotation(-90deg);
            }
        }
        .rtl.nav-visible &{
            display: block;
            left: 0;
            .transform(rotate(-90deg) translate(-50%, 50%));
            .transform-origin(0 50%);
            .btn_Contact{
                padding-left: 45px;
            }
            .btn_icon{
                left: 5px;
                .rotation(90deg);
            }
        }
    };
    .btn_Contact{
        display: block;
        padding: 3px 10px;
        .box-shadow(0px 0px 5px fade(@greyDark, 25%));
        position: relative;
        @media (max-width: 787px){
            padding-left: 45px;
            font-size: 1.2em;
        }
        @media (min-width: 788px){
            font-size: 1.4em;
        }
    }
    .btn_icon{
        position: absolute;
        width: 30px;
        .svgWrap{stroke-width: 6px;}
         @media (max-width: 787px){
            top: 4px;
            left: 6px;
        }
        @media (min-width: 788px){
            top: auto;
            bottom: 5px;
        }
    }
}

