.component-carousel{

    .carousel-slide{
        padding: 2em 4em;
        // min-height: 50vh;
        display: flex;
        align-items: end;
        background-size: cover;
        background-position: 50% 50%;
    }

    .bx-wrapper {
        margin-bottom: 50px;
        background-color: @greyLight;
        .bx-controls-direction a{
            &:focus{
                .btnFocus;
            }
        }
        .bx-prev, 
        .bx-next{
            // background: url(../UI/images/icons/carousel-arrows.png) no-repeat center;
            background-color: @buttonBkgColor;
            width: 40px;
            height: 50px;
            margin-top: -25px;
            .transition;
            .svg-icon{
                position: absolute;
                top: 50%;
                left: 5px;
                right: 5px;
                transform: translateY(-50%);
                .svgWrap{
                    stroke-width: 4px;
                }
            }
            &:hover,
            &:focus{        
                background-color: @buttonHoverBkgColor;
            }
        }
        
        .bx-prev{
            left: 0;
        }
        .bx-next{
            right: 0;
        }
        
    }

