// main: ../../less/styles.less

.component-dropdownsequence{
    .label-select-group{
        
        border: 1px solid @grey;
        .ds-label{
            padding: 10px 12px;
            border-bottom: 1px solid @grey;
            // border-bottom: none;
            label{
                font-weight: normal;
                margin-bottom: 0;;
            }
        }
        .dropdown-clear-btn-group{
            display: flex;
            flex-direction: row;
            .ds-select{
                // width: 100%;
                flex-grow: 1;
            }
            .ds-clear-btn{

                display: none;
                &:not(.disabled){
                    background-color: #000 !important;
                }
            }
            .option{
                display: inline-block;
                vertical-align: middle;
                position: relative;
                width: 100%;
                &.disabled{
                    .ui-icon{
                        display: none;
                    }
                }
                &.show-correct{
                    .tickCross{
                        display: none !important;
                        // .svgWrap{
                        //     stroke: @darkGrey; 
                        // }
                    }
                }
                &.correct {
                    background-color: @correctGreen;
                }
                &.incorrect, &.partlycorrect{
                    background-color: @incorrectRed;
                }
                &.correct, &.incorrect, &.partlycorrect{
                    .ui-selectmenu-text{
                        color: #fff;
                    }
                }
            }
        }
        .ui-front{
            z-index: 100;
        }
        .ui-selectmenu-button{
            display: inline-block;
            // overflow: hidden;
            position: relative;
            text-decoration: none;
            // border: 1px solid @grey;
            // border-right: 1px solid @grey;
            border-radius: 0px;
            font-weight: normal;
            // outline: none;
            cursor: pointer;
            padding: 10px 12px;
            width: 100% !important;
            // .ltr {
            //     padding-right: 10px;
            // }
            // .rtl {
            //     padding-left: 10px;
            // }
            .ui-icon{
                margin-top: -12px;
                position: absolute;
                top: 50%;
                display: block;
                text-indent: -99999px;
                overflow: hidden;
                background-repeat: no-repeat;
                width: 24px;
                height: 24px;
                .ltr &{
                    right: 1px;
                    left: auto;
                }
                .rtl &{
                    left: 1px;
                }
                &:before{
                    content: '';
                    width: 0px;
                    height: 0px;
                    border: 8px solid transparent;
                    border-top: 10px solid;
                    .applyModuleStylingPseudo(border-top-color);
                    position: absolute;
                    top: 7px;
                    left: 4px;
                }
            }
            .ui-selectmenu-text {
                padding: 2px;
                display: block;
                line-height: 1.4;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-weight: bold;
                white-space: normal !important;
                .ltr &{
                    padding-right: 22px;
                }
                .rtl &{
                    padding-left: 22px;
                }
            }
            &.ui-selectmenu-disabled{
                cursor: default;
            }
        }
    }
    
    
    
    .tickCross{
        position: absolute;
        // right: 2px;
        right: 0px;
        top: 50%;
        transform: translate(-50%, -50%);
        padding: 2px;
        .svgWrap{
            stroke-width: 6px;
        }
    }
}

.ui-selectmenu-menu{
    ul {
        .ui-menu-item{
            padding-left: 24px !important;
            padding-right: 24px !important;
            &.ui-state-disabled {
                color: @greyMid;
                cursor: default !important;
            }
        }
        
    }
}