// main: ../../less/styles.less

.component-panel{
    padding: 20px;
    
    &.lightGrey{
        background-color: @greyLight;
        color: @bodyColor;
        .panel-icon{
            .svgWrap{
                .applyModuleStyling(stroke);
            }
        }
    }
    &.white{
        background-color: @white;
        color: @bodyColor;
        .panel-icon{
            .svgWrap{
                .applyModuleStyling(stroke);
            }
        }
    }
    .panel-inner{
        &.has-icon{
            display: table;
            width: 100%;
            .panel-icon{
                display: table-cell;
                vertical-align: top;
                width: 50px;
                padding-left: 5px;
                padding-right: 5px;
                @media(min-width:768px){
                    width: 80px;
                }
            }
            .panel-content{
                display: table-cell;
                vertical-align: top;
                padding-left: 5px;
                padding-right: 5px;
            }
        }
    }
    &.drop-shadow{       
        box-shadow: 0 5px 13px 0 rgba(0,0,0,.14);
    }
}