// main: styles.less

// functions to generate colours and classes
.generateColors(@name){
    @color: "color@{name}";
    .color@{name}{
        color: @@color !important;
    }
    .bkgColor@{name}{
        background-color: @@color !important;

        a{
            // color: @linkColor;
            color: contrast(@@color, @white, @linkColor);

            &:hover, &:focus{
                color: contrast(@@color, darken(@white, 10%), @linkHoverColor);
                // .contrast-check(@linkHoverColor, @@color);

            }
        }
    }
    
    .bkgColorTransparent@{name},
    .bkgColorTransparent@{name}80{
        background-color: fade(@@color, 80%) !important;
    }
    .bkgColorTransparent@{name}90{background-color: fade(@@color, 90%) !important;}
    .bkgColorTransparent@{name}70{background-color: fade(@@color, 70%) !important;}
    .bkgColorTransparent@{name}60{background-color: fade(@@color, 60%) !important;}
    .bkgColorTransparent@{name}50{background-color: fade(@@color, 50%) !important;}
    .bkgColorTransparent@{name}40{background-color: fade(@@color, 40%) !important;}
    .bkgColorTransparent@{name}30{background-color: fade(@@color, 30%) !important;}
    .bkgColorTransparent@{name}20{background-color: fade(@@color, 20%) !important;}
    .bkgColorTransparent@{name}10{background-color: fade(@@color, 10%) !important;}

    .overspill-bkgColor@{name}{
        &:before{
            background-color: @@color !important;
        }
    }
    
}
//.generateColorsByName(blue);
.generateColorsByName(@name){
    @color: "@{name}";
    .@{name}Color{
        color: @@color !important;
    }
    .@{name}Bkg{
        background-color: @@color !important;
    }
}

// callout color mixin
.callout-color(@color){
    &:not(.callout-outline){
        background-color: @color;
        //color: #000;
    }

    &.top::before           {border-top-color: @color;}
    &.bottom::before        {border-bottom-color: @color;}
    &.left::before          {border-left-color: @color;}
    &.right::before         {border-right-color: @color;}
    &.top-left::before      {border-top-color: @color;}
    &.top-right::before     {border-top-color: @color;}
    &.bottom-left::before   {border-bottom-color: @color;}
    &.bottom-left20::before {border-bottom-color: @color;}
    &.bottom-right::before  {border-bottom-color: @color;}
    &.bottom-right20::before{border-bottom-color: @color;}
    &.left-top::before      {border-left-color: @color;}
    &.right-top::before     {border-right-color: @color;}

    &.callout-outline{
        &.left{border-right-color: @color;}
        &.right{border-left-color: @color;}
        &.top{border-bottom-color: @color;}
        &.bottom{border-top-color: @color;}

        .svgWrap{
            stroke-width: 4px;
            stroke: @color;
            fill: @color;
        }
    }

    &.callout-border{
        border: 3px solid @color;
        background-color: #fff;
        color: @color;
    }
}


//function to generate colour classes for module
// argumen
.moduleColorClassStyling(@name, @color, @secondaryColor){
    .@{name}, #content .@{name}{.moduleStyling(@color, @secondaryColor)}
}

.moduleStyling(@name, @secondaryColor){
    @color: "@{name}";
    @colorLight: #f00;//lighten(@@color, 10%);//"@{name}Light";
    @colorDark: #f00;//darken(@@color, 10%);//"@{name}Dark";
    .moduleColor{
        color: @@color !important;
    }
    .moduleBkg{
        background-color: @@color !important;
    }
    .moduleBkgTransparent{
        background-color: fade(@@color, 80%) !important;
    }

    .moduleColorOverlay{
        &:before{
            content: '';
            background-color: fade(@@color, 90%);
            width: 100%;
            height: 100%;
            position: absolute;
        }
    }
    .text-gradient{
        background: -webkit-linear-gradient(90deg, @colorDark, @colorLight);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    
    //#content &,
    //.modal &{
        a{
            color: @@color;
            //border-bottom-color: @@color;
            &:hover, &:focus{
                color: @colorLight;
            }
            /*&[data-popover]{
            //     background-color: @@color;
            //     color: @secondaryColor;
                border-color: fade(@@color, 50%) !important;
                &:hover, &:focus{
                    // border-color: @colorLight !important;
                    color: @@color;
                    background-color: fade(@@color, 25%) !important;
                }
            }*/
        }
        .contentLight{
            a{
                color: @secondaryColor;
                //border-bottom-color: @@color;
                &:hover, &:focus{
                    color: @colorLight;
                }
                /*&[data-popover]{
                    border-color: fade(@secondaryColor, 50%) !important;
                    &:hover, &:focus{
                        // border-color: @colorLight !important;
                        color: @secondaryColor;
                        background-color: fade(@secondaryColor, 25%) !important;
                    }
                }*/
            }
        }
    //}
    
    .btn, .moduleBtn, &.moduleBtn {
        background-color: @@color !important;
        color: @secondaryColor !important;
        .transition();
        &:not(.disabled){
            &:hover{
                background-color: @colorLight !important;
            }
        }
        .svgWrap{
            stroke: @secondaryColor;
        }
        &:not(.component){
            &.active{
                background-color: @colorLight !important;
                .box-shadow(none);
                &:not(.disabled){
                    &:hover{
                        background-color: @colorLight !important;
                    }
                }
            }
            &.done{
                &:not(.active){
                    background-color: @colorDark !important;
                    &:not(.disabled){
                        &:hover{
                            background-color: @colorLight !important;
                        }
                    }
                }
            }
        }
        &.moduleBtnInvert{
            background-color: @secondaryColor !important;
            color: @@color !important;
            .svgWrap{
                stroke: @@color;
            }

            &:not(.disabled){
                &:hover{
                    background-color: tint(@@color,75%) !important;
                }
            }
            &:not(.component){
                &.active{
                    background-color: tint(@@color,75%) !important;
                    .box-shadow(none);
                    &:not(.disabled){
                        &:hover{
                            background-color: tint(@@color,75%) !important;
                        }
                    }
                }
                &.done{
                    background-color: @secondaryColor !important;
                    &:not(.active){
                        color: @colorDark !important;
                        &:not(.disabled){
                            &:hover{
                                color: @@color !important;
                                background-color: tint(@@color,75%) !important;
                            }
                        }
                    }
                }
            }
        }
        
        &.backBtn{
            background-color: transparent !important;
            color: @greyDark !important;
            .ltr &{
                &:before{
                    border-right-color: @@color !important;
                }
            }
            .rtl &{
                &:before{
                    border-left-color: @@color !important;
                }
            }
            &:not(.disabled){
                &:hover{
                    background-color: transparent !important;
                }
            }
        }
        &.continueBtn,
        &.btn-sequence.btn-inline,
        &.btn-active-arrow-right.active{
            .ltr &{
                &:before{
                    border-left-color: @@color !important;
                }
                &:not(.disabled){
                    &:hover{
                        &:before{
                            border-left-color: @colorLight !important;
                        }
                    }
                }
                &.moduleBtnInvert{
                    &:before{
                        border-left-color: @secondaryColor !important;
                    }
                    &:not(.disabled){
                        &:hover{
                            &:before{
                                border-left-color: tint(@@color,75%) !important;
                            }
                        }
                    }
                }
            }
            .rtl &{
                &:before{
                    border-right-color: @@color !important;
                }
                &:not(.disabled){
                    &:hover{
                        &:before{
                            border-right-color: @colorLight !important;
                        }
                    }
                }
                
                &.moduleBtnInvert{
                    &:before{
                        border-right-color: @secondaryColor !important;
                    }
                    &:not(.disabled){
                        &:hover{
                            &:before{
                                border-right-color: tint(@@color,75%) !important;
                            }
                        }
                    }
                }
            }
        }
        
        &.btn-active-arrow-bottom.active{
            &:before{
                border-top-color: @@color !important;
            }
            &:not(.disabled){
                &:hover{
                    &:before{
                        border-top-color: @colorLight !important;
                    }
                }
            }
        }

        &.btn-sequence.btn-block{
            &:before{
                border-top-color: @@color !important;
            }
            &:not(.disabled){
                &:hover{
                    &:before{
                        border-top-color: @colorLight !important;
                    }
                }
            }
        }
    }
    
    .feedback{
        border-top-color: @@color;
    
        h1,h2,h3,h4,h5,h6{
            color: @@color;
        }
//        :not(.btn){
//        }
    }
    .component-image{

        .svgWrap{
            stroke: @@color;
        }
    }
    
    // colour the callout based on the module colour
    .callout,
    .component-callout{
        .callout-color(@@color);
        &.callout-reveal{
            .callout-color(@colorDark);            
        }
        &.action{
            &:not(.disabled){
                &:hover{
                    .callout-color(@colorLight);
                }
            }
        }
    }
    
    .contentPanel,
    .component-panel{
        background-color: @@color;
        color: @secondaryColor;
        .svgWrap{
            stroke: @secondaryColor;
        }
    }

    .popover{
        border-color: @@color;
        &.top{
            >.arrow{
                border-top-color: @@color;
            }
        }
        &.bottom{
            >.arrow{
                border-bottom-color: @@color;
            }
        }
        a{
            color: @@color !important;
        }
    }
    
    .colorStrip{
        background-color: @@color;
        border-top: @colorDark 5px solid;
        border-bottom: @colorDark 5px solid;
        .gradient(@@color, @@color, @colorLight);
        &.pattern{
            .imagegradient('../UI/images/patterns/@{name}_carbon_open_pattern_tile.png', @@color, @@color, @colorLight);
            
        }
    }
    .splashPage.fullheight{
        //background-color: fade(@@color, 80%);
    }
//    @pulseGlow: ~"pulseGlow@{num}";
//    .pulseGlow{
//        .animation(@pulseGlow 1s infinite);
//    }
    // .video-js {
    //     .vjs-control-bar,
    //     .vjs-big-play-button{
    //         background-color: fade(@@color, 70%);
    //     }
    //     .vjs-slider{
    //         background-color: @colorDark;            
    //     }
    //     .vjs-text-track-display > div > div > div {
    //         background-color: fade(@@color, 70%) !important;
    //     }
    // }
}

.applyModuleStylingMixin(@property, @col, @colorType: '', @opacity: 100%){
    @color: "@{col}@{colorType}";
    @{property}: fade(@@color, @opacity);    
}

.applyModuleStyling(@property, @colorType: '', @opacity: 100%){

    each(@namecolors, {
        &.@{key}, .@{key}&, .@{key} &{.applyModuleStylingMixin(@property, @value, @colorType, @opacity);}
    });
}
.applyModuleStylingPseudo(@property, @colorType: '', @opacity: 100%){
    each(@namecolors, {
        .@{key}&, .@{key} &{.applyModuleStylingMixin(@property, @value, @colorType, @opacity);}
    });
}

.applyModuleStylingToElement(@property, @colorType: '', @opacity: 100%){
    each(@namecolors, {
        &.@{key}{.applyModuleStylingMixin(@property, @key, @colorType, @opacity);}
    });
}

.applyModuleStylingToChildElement(@element, @property, @colorType: '', @opacity: 100%){
    each(@namecolors, {
        &.@{key} .@{element}{.applyModuleStylingMixin(@property, @key, @colorType, @opacity);}
    });
}

