.debug{
    color: #999;
    font-size: 14px;
    position: fixed;
    top: 0;
    background-color: #111111;
    padding: 2px;
    width: 100%;
    left: 0;
    z-index: 20000;
    .debug-container{
        a{
            color: #fff;
        }
        .debug_Btn{
            &.disabled{
                color: #ccc;
                cursor: default;
            }
            // &.debug_btn_Reload{
            //     float: right;
            // }
        
            &.pageLockToggle,
            &.animToggle{
                display: inline-block;
                vertical-align: top;
                margin: 0 1px;
                width: 20px;
                height: 21px;
                background-size: contain;
                background-repeat: no-repeat;
                .transition(0s);
            }
            &.pageLockToggle{                
                background-image: url(https://livelogic-html-framework.s3.eu-west-2.amazonaws.com/debug/icons/padlock.png);
                background-size: 200% 100%;
                background-position: 100% 0;
                &.locked{
                    background-position: 0 0;
                }
            }
            &.animToggle{
                background-image: url(https://livelogic-html-framework.s3.eu-west-2.amazonaws.com/debug/icons/anim_debug.png);
                &:not(.active){
                    opacity: .3;
                }
            }
        }
        .debug-icon-wrapper{
            float: right;
            .icon-liveedit,
            .icon-livecdn,
            .icon-liveupdate{
                width: 20px;
                height: 21px;
                background-size: contain;
                background-repeat: no-repeat;
                display: inline-block;
                vertical-align: top;
                margin: 0 1px;
                &:not(.active){
                    opacity: .3;
                }
            }
            .icon-liveedit{background-image: url(https://livelogic-html-framework.s3.eu-west-2.amazonaws.com/debug/icons/liveedit_hex.png)}
            .icon-livecdn{background-image: url(https://livelogic-html-framework.s3.eu-west-2.amazonaws.com/debug/icons/livecdn_hex.png)}
            .icon-liveupdate{background-image: url(https://livelogic-html-framework.s3.eu-west-2.amazonaws.com/debug/icons/liveupdate_hex.png)}
        }
    }
}

@debugRed: #bd0909;
@debugAmber: #cc5f07;
@debugGreen: #12ad12;

.anim{
    .debug-on.anim-debug-on &{
        position: relative;
    }
    .anim-debug{
        display: none;
        position: absolute;
        background-color: #000;
        color: #fff;
        .ltr &{
            left: -2px;
        }
        .rtl &{
            right: -2px;
        }
        .fadeInLeft&{
            .ltr &{
                left: auto;
                right: -2px;
            }
            .rtl &{
                right: auto;
                left: -2px;
            }
        }
        bottom: 100% - 2px;
        align-items: center;
        padding: 2px;
        font-size: 12px;
        font-weight: normal;
        gap: 2px;
        z-index: 9000;
        &:hover{
            z-index: 9001;
        }

        .anim-debug-item{
            width: 1em;
            height: 1em;
            border-radius: 50%;
            border: 1px solid #fff;
            &.anim-debug-item-anim{
                background-color: @debugRed;
                .animating&{
                    background-color: @debugAmber;
                }
                .animated&{
                    background-color: @debugGreen;
                }
            }
            &.anim-debug-item-inview{
                background-color: @debugRed;
                .animready&{
                    background-color: @debugAmber;
                }
                .animinview&{
                    background-color: @debugGreen;
                }
            }
        }

        .debug-on.anim-debug-on &{
            display: flex;
        }
        .component.complete&{
            background-color: #028f02;
        }
    }
}