// main: ../../less/styles.less

.component-tab{
    .panel{
        margin-bottom: 0;
        background-color: transparent;
        border: none;
        border-radius: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
    }
    .tab-group{
        position: relative;
    }
    .tab-item{
        position: relative;
        border: solid 2px #fff;
        margin-bottom: 10px;
    }
    
    .tab-button{
        .clearfix();
        display: block;
        background-color: @greyLight;
        color: @color1;
        .tab-heading{
//            .bold();
            padding:10px;
            .transition();
        }
        .tab-icon{
            //padding: 20px;
        }
        
        &:hover{
//            &:before{
//                background-position: -10px -110px;
//            }
            .tab-heading{
                //background-color: @color2;
            }
        }
        &:hover,
        &:focus{
            text-decoration: none;
        }
//        &:before{
//            content:'';
//            position: absolute;
//            top: 50%;
//            right: -15px;
//            margin-top: -16px;
//            background: url("../UI/images/plus_minus_sprite.png") no-repeat scroll transparent;
//            background-position: -10px -160px;
//            height: 33px;
//            width: 33px;
//            z-index: 1;
//        }
        &.collapsed{
            // mobile version
//            .tab-heading{
//                background-color: @greyLight;
//                color: @color1;
//            }
            &:hover{
                &:before{
                    background-position: -10px -10px;
                }
                .tab-heading{
                    //background-color: @greyLight;
                }
            }
            &:before{
                background-position: -10px -60px;
            }
        }
        
//        &:hover{
//            &:before{
//                right: 0;
//            }
//        }
    }
    .tab-reveal {
        padding: 20px;
        // background-color: @greyLight;
        @media (min-width: 768px){
            //padding: 30px;            
            border-top: 2px @color1 solid;
        }
    }
    .tabWrap{
//        .ltr &{
//            margin-left:30px;
//        }
//        .rtl &{
//            margin-right:30px;
//        }
        .tab-button{
            width: 30%;
            // .tab30&{
            //     width: 30%;
            // }
            img {
                opacity: .5;
                .transition();
            }
            &.active{
                img{
                    opacity: 1;
                }                
            }
            .ltr &{
                float:left;
                //margin-right: 3px;
            }
            .rtl &{
                float:right;
                //margin-left: 3px;
            }
            
            .tab-heading{
                text-align: center;
                // display: none;
                padding: 20px 5px;
            }
            &:not(.active){
                //background-color: #fff !important;

            }
        }
        &.noicon{
            .tab-button{
                &.active{
                    padding:5px 0px;
                }
                &:not(.active){
                    background-color: @color1Dark;
                    margin-top: 10px;
                }
            }
        }
    }
    .tabAccordionWrap{    
        clear: both;
        .tab-button{
            margin-top: 2px;
            .tab-icon{
                width: 60px;
                float: left;
                margin: 2px;
                margin-right: 10px;
                padding: 0;
                
                .ltr &{
                    float:left;
                }
                .rtl &{
                    float:right;
                }
                
            }
        }
    }
    .tabWrap,
    .tabAccordionWrap{
        &.noicon{
            .tab-icon{
                display: none;
            }
            .tab-heading{
                display: block;
            }
        }
    }
}