// main: ../../less/styles.less

.component-accordion{
    .accordion-group{
        margin-bottom: 1em;
        
        .accordion-item{
            margin-bottom: 5px;
        }
        .accordion-button{
            position: relative;
            display: block;
            width: 100%;
            border: none;
            text-align: inherit;        
            padding: 1em;
            .ltr &{
                padding-right: 40px;
            }
            .rtl &{
                padding-left: 40px;
            }
            .bkgColorLightGrey &{
                background-color: #fff;
            }
            
            &:focus{
                .btnFocus;
            }
            .accordion-heading {
                h2, h3 {
                    font-size: 1.2rem !important;
                }
            }

            .accordion-icon{
                width: 20px;
                //float: left;
                //margin:5px;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                // margin-top: -15px;
                .ltr &{
                    right: 10px;
                }
                .rtl &{
                    left: 10px;
                }
                .svgWrap{
                    // .applyModuleStyling(stroke);
                    stroke: @greyDark;
                    stroke-width: 5px;
                }
            }
        }
        .accordion-reveal{
            padding: 1em;
            &.collapsed{
                display: none;
            }
        }
    }
}