// main: ../../less/styles.less

.component-text{
    a{
        text-decoration: underline;
    }
    h2{
//        margin-bottom: 20px;
    }
    .titlePageTitle{
        font-size: 7em;
        .CSSLock(3em, 768px, 7em, 1200px);
        padding: 40px 0;
        border-top: 5px solid @color1;
        border-bottom: 5px solid @color1;
    }
    &.headerText{
        h2{
            font-size: 10em;
            .CSSLock(3em, 768px, 10em, 1200px);
        }
        p{
            font-size: 2.2em;
            .CSSLock(1.2em, 768px, 2.2em, 1200px);
        }
    }
}