.angleTop,
.angleTopLeft,
.angleTopRight,
.angleBottom,
.angleBottomLeft,
.angleBottomRight {
	position: relative;
	// overflow-x: hidden; // added as 100vw includes the vertical scrollbar, so end up with horizontal scroll so need to prevent
}
.angleTop,
.angleTopLeft,
.angleTopRight {
	&:before {
		content: "";
		position: absolute;
		top: 0;
		width: 0;
		height: 0;
		border-top: 4vh solid #fff;
		z-index: 1;
	}
	.section {
		padding-top: 100px;
	}
}

.angleTopLeft {
	&:before {
		left: 0;
		border-right: calc(100vw - 20px) solid transparent;
	}
}
.angleTop,
.angleTopRight {
	&:before {
		right: 0;
		border-left: calc(100vw - 20px) solid transparent;
	}
}
.angleBottom,
.angleBottomLeft,
.angleBottomRight {
	&:after {
		content: "";
		position: absolute;
		bottom: 0;
		width: 0;
		height: 0;
		border-bottom: 4vh solid #fff;
		z-index: 1;
	}
	.section {
		padding-bottom: 100px;
	}
}
.angleBottom,
.angleBottomLeft {
	&:after {
		left: 0;
		border-right: calc(100vw - 20px) solid transparent;
	}
}
.angleBottomRight {
	&:after {
		right: 0;
		border-left: calc(100vw - 20px) solid transparent;
	}
}

