.callout,
.component-callout {
	&.callout-quotation {
		.quotation {
			padding: 25px 45px;
			@media (max-width: 767px) {
				padding: 35px 10px;
			}
			.callout-quotation-large& {
				padding: 45px;
				@media (max-width: 767px) {
					padding: 35px 10px;
				}
			}
		}
		.quotation-icon-ldquo,
		.quotation-icon-rdquo {
			position: absolute;
			width: 30px;
			.callout-quotation-small& {
				width: 20px;
			}
			.callout-quotation-large& {
				opacity: 0.3;
				width: 100px;
				@media (max-width: 767px) {
					width: 50px;
				}
			}
			.svgWrap {
				stroke-width: 4px;
				// stroke: @blue;
				// fill: @blue;
			}
		}
		.quotation-icon-ldquo {
			left: 5px;
			.ltr & {
				top: 5px;
			}
			.rtl & {
				bottom: 5px;
			}
			.callout-quotation-large& {
				.ltr & {
					top: -10px;
				}
				.rtl & {
					bottom: -10px;
				}
			}
		}
		.quotation-icon-rdquo {
			right: 5px;
			.ltr & {
				bottom: 5px;
			}
			.rtl & {
				top: 5px;
			}
			.callout-quotation-large& {
				.ltr & {
					bottom: -10px;
				}
				.rtl & {
					top: -10px;
				}
			}
		}
	}
}

ul {
	li {
		&::marker {
			color: @color1;
		}
	}
	&.chevronBullets,
	&.tickBullets {
		list-style: none;
		> li {
			position: relative;
			&::before {
				content: "";
				position: absolute;
				width: 0.8em;
				height: 0.8em;
				top: 0.2em;
			}
		}
	}
	&.chevronBullets {
		> li {
			&::before {
				background-image: url(../UI/images/icons/icon-chevrons.png);
				background-size: 200% 500%;
				.ltr & {
					left: -1em;
					background-position: 100% 100%;
				}
				.rtl & {
					right: -1em;
					background-position: 0% 100%;
				}
			}
		}
	}
	&.tickBullets {
		> li {
			&::before {
				top: 0.1em;
				width: 1.2em;
				height: 1.2em;
				background-image: url(../UI/images/icons/icon-tickcross.png);
				background-size: 300% 600%;
				background-position: 100% 20%;
				.ltr & {
					left: -1.5em;
				}
				.rtl & {
					right: -1.5em;
				}
			}
		}
	}
}

