// main: styles.less

// animation

/*Animation Declaration */
.anim {
    &:not(.animated){
        opacity: 0;
        &.btn.disabled{
            opacity: 0;
        }
    }
}
.cssanimations {
    .anim:not(.invisible):not(.visibility-hidden) {
        -webkit-animation-duration: 0.8s;
           -moz-animation-duration: 0.8s;
             -o-animation-duration: 0.8s;
                animation-duration: 0.8s;
        -webkit-animation-fill-mode: both;
           -moz-animation-fill-mode: both;
             -o-animation-fill-mode: both;
                animation-fill-mode: both;
        //opacity: 1 !important;
        &.animready.animinview{
//        &:not(.disabled).animready.animinview{
            &.fadeInRight {
                -webkit-animation-name: fadeInRight;
                   -moz-animation-name: fadeInRight;
                     -o-animation-name: fadeInRight;
                        animation-name: fadeInRight;
            }
            &.fadeInLeft {
                -webkit-animation-name: fadeInLeft;
                   -moz-animation-name: fadeInLeft;
                     -o-animation-name: fadeInLeft;
                        animation-name: fadeInLeft;
            }
            &.fadeInUp {                
                -webkit-animation-name: fadeInUp;
                   -moz-animation-name: fadeInUp;
                     -o-animation-name: fadeInUp;
                        animation-name: fadeInUp;
            }
            &.fadeInDown {                
                -webkit-animation-name: fadeInDown;
                   -moz-animation-name: fadeInDown;
                     -o-animation-name: fadeInDown;
                        animation-name: fadeInDown;
            }
            &.fadeIn {                                
                -webkit-animation-name: fadeIn;
                   -moz-animation-name: fadeIn;
                     -o-animation-name: fadeIn;
                        animation-name: fadeIn;
            }
            &.disabled{
                //opacity: 0.5 !important;        
            
                &.fadeInRight,
                &.fadeInLeft,
                &.fadeInUp,
                &.fadeInDown,
                &.fadeIn{
                    -webkit-animation-name: fadeInDisabled;
                       -moz-animation-name: fadeInDisabled;
                         -o-animation-name: fadeInDisabled;
                            animation-name: fadeInDisabled;
                }
            }
        }
        &.animated {
            -webkit-animation: none !important;
               -moz-animation: none !important;
                 -o-animation: none !important;
                    animation: none !important;
            &:not(.disabled){
                opacity: 1;
//                opacity: 1 !important;
            }
//            &.disabled{
//                opacity: 0.5;                
//            }
        }

        &.anim-delay1{.animation-delay(1s);}
        &.anim-delay2{.animation-delay(2s);}
        &.anim-delay3{.animation-delay(3s);}
        &.anim-delay4{.animation-delay(4s);}
        &.anim-delay5{.animation-delay(5s);}
    }

}
//.cssanimations .anim {
//    opacity: 0 !important;
//}
.not-animated.anim,
.not-animated .anim,
.children-not-animated .anim{
    opacity: inherit !important;
}

