@numCols = 12;

.setColsGap(@gap){
    margin-left: -(@gap);
    margin-right: -(@gap);
    .col,
    .col-xs,
    .col-sm,
    .col-md,
    .col-lg,
    .col-xs-1, .col-xs-2, .col-xs-3, .col-xs-4, .col-xs-5, .col-xs-6, .col-xs-7, .col-xs-8, .col-xs-9, .col-xs-10, .col-xs-11, .col-xs-12,
    .col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12,
    .col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12,
    .col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12 {
        padding-left: @gap;
        padding-right: @gap;
    }
    .row{        
        margin-left: -(@gap);
        margin-right: -(@gap);
    }
}
.row{
    .setColsGap(5px);
    &.gap0{
        .setColsGap(0px);
    }
    &.gap2{
        .setColsGap(1px);
    }
    &.gap4{
        .setColsGap(2px);
    }
    &.gap6{
        .setColsGap(3px);
    }
    &.gap10{
        .setColsGap(5px);
    }
    &.gap20{
        .setColsGap(10px);
    }
    &.gap30{
        .setColsGap(15px);
    }
    &.gap40{
        .setColsGap(20px);
    }
    &.gap50{
        .setColsGap(25px);
    }
    &.gap60{
        .setColsGap(30px);
    }
    &.gap70{
        .setColsGap(35px);
    }
}

.bootstrapColsRTL(@viewport){
    // loop through all 12 cols
    .bootstrapColRTL(@viewport, 1);
}

.bootstrapColRTL(@viewport, @cols) when (@cols =< 12){
    .col-@{viewport}-@{cols}{float: right;}

    .col-@{viewport}-offset-@{cols}{margin-left: 0; margin-right: @cols/12*100%;}

    .col-@{viewport}-push-@{cols}{left: auto; right: @cols/12*100%;}

    .col-@{viewport}-pull-@{cols}{left: auto; right: -@cols/12*100%;}

    // recursively loop through remaining cols
    .bootstrapColRTL(@viewport, (@cols + 1));
}

.navbar-header{    
    float: left;
}
.navbar-nav{
    float: left;
    margin: 0;
    > li{
        float: left;
    }
}
.navbar-right {
    float: right !important;
    margin-right: -5px;
  }

.rtl{
    // right to left styles
    direction: rtl;
    
    .bootstrapColsRTL(xs);
    
    @media(min-width: 768px){
        .bootstrapColsRTL(sm);
    }
    @media(min-width: 992px){
        .bootstrapColsRTL(md);
    }
    @media(min-width: 1200px){        
        .bootstrapColsRTL(lg);
    }
    
    .pull-right{
        float: left !important;
    }
    .pull-left{
        float: right !important;
    }
    .navbar-header{
        @media(min-width: 768px){
            float:right;
        }
    }
    .navbar{
        > .container,
        > .container-fluid{
            .navbar-brand{
                float:right;
                @media(min-width: 768px){
                    margin-left: 10px;
                    margin-right: -15px;
                }
            }
        }
    }
    .navbar-textwrap{
        float:right;
        margin-left: 0px;
        margin-right: 10px;
    }
    .nav{
        padding-right: 0;
        
    }
    // @media(min-width: 768px){
        .navbar-nav{
            > li{
                float: right;
            }
        }
        .navbar-right {
            float: left !important;
            margin-left: -5px;
            .dropdown-menu{
                left: 0;
                right: auto;
            }
        }
    // }
    .navbar-toggle{
        float: left;
        margin-left: 5px;
        left: 0;
        left: 0;
        right: auto;
    }
}
// *************************
// bootstrap 3 to 4 flex conversion
// *************************

@media (max-width: @deviceWidthMaxXS){
    .flex-row-xs{
        .flexRow();
        .flexRowCols(xs);
    }
    .columnsX(xs);
}

// flex-row classes for small and up only
@media (min-width: @deviceWidthSM){
    // .flex-row{
    //     .flexRow();
    //     .flexRowCols(sm);
    //     .flexRowCols(md);
    //     .flexRowCols(lg);
    // }
    .columnsX();
}
@media (min-width: @deviceWidthSM) and (max-width: @deviceWidthMaxSM){
    .flex-row,
    .flex-row-sm{
        .flexRow();
        .flexRowCols(xs);
        .flexRowCols(sm);
    }
    .columnsX(sm);
}
@media (min-width: @deviceWidthMD) and (max-width: @deviceWidthMaxMD){
    .flex-row,
    .flex-row-md{
        .flexRow();
        .flexRowCols(xs);
        .flexRowCols(sm);
        .flexRowCols(md);
    }
    .columnsX(md);
}
@media (min-width: @deviceWidthLG){
    .flex-row,
    .flex-row-lg{
        .flexRow();
        .flexRowCols(xs);
        .flexRowCols(sm);
        .flexRowCols(md);
        .flexRowCols(lg);
    }
    .columnsX(md);
}
.flexRow(){
    display: flex;
    flex-wrap: wrap;

    &:after,
    &:before{
        display: none; // added as Safari wraps last element when flex-wrap: wrap
    }
}
.flexRowCols(@viewport){
    // loop through all 12 cols
    .flexRowCol(@viewport, 1);

    .col-@{viewport}{
        flex: 0;
        flex-grow: 1;
        max-width: 100%;
    }
}
.flexRowCol(@viewport, @cols) when (@cols =< 12){
    .col-@{viewport}-@{cols}{flex: 0 0 @cols/12*100%;}
    // recursively loop through remaining cols
    .flexRowCol(@viewport, (@cols + 1));
}


// flex align properties
.align-self-start{
    align-self: start;
}
.align-self-center{
    align-self: center;
}
.align-self-end{
    align-self: end;
}
.align-self-stretch{
    align-self: stretch;
}

.align-items-center{
    align-items: center;
}
.align-items-end{
    align-items: end;
}
.align-items-stretch{
    align-items: stretch;
}
.d-flex{
    display: flex!important;
}

.columnsX(@viewport: ''){
    
    .createColumnsX(5, @viewport);
    .createColumnsX(7, @viewport);
}

.createColumnsX(@cols, @viewport) when (@viewport = ''){
    .columns-@{cols}{
        >div{
            width: 100%/@cols;
            float: left;
        }
    }
}

.createColumnsX(@cols, @viewport) when not (@viewport = ''){
    .columns-@{viewport}-@{cols}{
        >div{
            width: 100%/@cols;
            float: left;
        }
    }
}


// overspill columns
.overspill{
    overflow: hidden;
    padding-top: 0 !important;
    padding-bottom: 0 !important;

    margin-top: 50px;
    margin-bottom: 50px;

    > .container > .row > div{
        //&:not(.overspill-col-image){
            
            &:before{
                content: '';
                position: absolute;
                top: 0;
                width: 100vw;
                height: 100%;

                // background-color: #e3e3e3;
            }
            &.overspill-moduleBkgColor{
                &:before{
                    .applyModuleStylingPseudo(background-color);
                }                
            }

            &.overspill-self{
                position: relative;                
            }

            &.overspill-inactive{
                padding: 0 !important;
                min-height: auto !important;
            }

        //}
        
        &.overspill-col-min-height{
            &:before{
                content: '';
                display: block;
                // @media(min-width: 768px){
                    padding-bottom: 56.25%;
                // }
            }
        }
    
        // .overspill-left{
        &:first-child{
            &:before{
                .ltr &{
                    right: 0;
                }
                .rtl &{
                    left: 0;
                }
            }
            .overspill-image{
                .ltr &{
                    right: 0;
                }
                .rtl &{
                    left: 0;
                }
            }
        }
        // .overspill-right{
        &:last-child{
            &:before{
                .ltr &{
                    left: 0;
                }
                .rtl &{
                    right: 0;
                }
            }
            .overspill-image{
                .ltr &{
                    left: 0;
                }
                .rtl &{
                    right: 0;
                }
            }
        }
    }
}

