// main: ../../less/styles.less

.component-media{

    // border-radius: 10px 10px 0 0;
    // overflow: hidden;
    .video-js{
        box-shadow: 0px 0px 10px rgba(0,0,0,.1);
        .vjs-big-play-button{
            height: 3em;
            margin-top: -1.5em;
            border: none;
            border-radius: 50%;
            .vjs-icon-placeholder{
                &:before{
                    content: '';
                }
            }
        }
        &.vjs-big-play-none{
            .vjs-big-play-button {
                display: none;
            }
        }
        &.hide-video-area{
            max-height: 3em;
        }
        .vjs-subtitles-button{
            @media (max-width: @maxSM) {
                display: none;
            }
        }
    }
    &.transcript-color-dark{
        .transcript-reveal{
            color: @greyDark;
        }
    }
    .vjs-audio-no-poster {
        width: 100%;
        .vjs-text-track-display {
            div{
                font-size: 1rem !important; /* You can adjust this value */
            }
        }
    }
    video{
        // width: 100%;
        // height: auto;

    }
    &.fullscreen{
        margin-bottom: 0;
        .video-js{
            
            overflow: hidden;
            min-height: @viewportVisibleHeight;
            video{
                position: absolute;
                top: 50%;
                left: 50%;
                min-width: 100%;
                min-height: 100%;
                width: auto;
                height: auto;
                transform: translate(-50%, -50%);
            }
        }
    }
    &.nextOnComplete,
    &.nextSlideOnComplete{
        .video-js{
            &.vjs-ended{
                .vjs-big-play-button{
                    display: none;
                }
            }
        }
    }
    .fallbackMsg{
        font-style: italic;
        margin-top: 10px;
        font-size: .8em;
    }
    
    .accordion-group{
        // border-radius: 0 0 10px 10px;
        // overflow: hidden;
    }
    .accordion-button{
        position: relative;
        margin-top: 10px;
//        &:hover{
//            color: @color4;
//        }
        // background-color: #fff;
        // font-style: normal;
        // display: block;
        display: inline-block;
        padding: 10px;
        &.collapsed{
            .accordion-heading-text{
                display: none;
                &.accordion-heading-text-collapsed{
                    display: block;
                }
            }
        }
        &:not(.collapsed){            
            background-color: @greyLight;
            .accordion-heading-text{
                display: block;
                &.accordion-heading-text-collapsed{
                    display: none;
                }
            }
        }
        
        // text-transform: uppercase;
        &:hover,
        &:focus{
            text-decoration: none;
        }
        .accordion-heading{
            font-weight: bold;
            
            // padding: 10px 0px;
            // color: #fff;
            //background-color: @color1;
            &:before{
            }
        }
    }
    .accordion-reveal {
        padding: 20px;
        word-break: break-word;
        // padding: 10px 25px;
        background-color: @greyLight;        
        // color: @bodyColor;
        //background-color: lighten(@color1,50%);
    }

    .transcript-toggle-btn{
        position: relative;
        background-color: @greyLight !important;
        color: @hsbcBlack !important;
        &:not(.btn-transcript-only){
            display: none;
        }
        @media (max-width: @maxSM) {
            display: inline-block;
        }
        &:not(.disabled){
            &:hover{
                background-color: darken(@greyLight, 10%) !important;
            }
        }
        padding-right: calc(20px + 1.25rem);
        .svgWrap {
            stroke: @black !important;
        }
        font-weight: 400;
        .transcript-icon{
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(-50%, -50%);
            stroke: @black !important;
            width: 1.25rem;
            display: inline-block;
            &.hide-transcript-icon{
                display: none;
            }
        }
    }

    .transcript-reveal{
        padding: 20px;
        word-break: break-word;
        display: none;
        background-color: @greyLight;
        color: @greyDark;    
        .transcript-title{
            .font1Light;
            font-size: 1.75rem;
            margin-bottom: 10px;
            display: block;
            
            @media (max-width: @maxSM) {
                display: none;
            }
        }
    }
}
// end component