// main: ../../less/styles.less

.component-mcqtawa{

    @optionHoverBgColor: #ededed;

    .options{
        &.scq{
            .option{
                .checkboxIcon{
                    border-radius: 50%;
                }
            }
        }
        &.mcq{
            .option{
                .checkboxIcon{
                    border-radius: 3px;
                }
            }
        }
    }

    .option{
        
        position: relative;
        margin-bottom: 10px;
        cursor: pointer;
        .transition();
        border: 1px solid @grey;
        background-color: #fff;
        &.disabled {
            &:not(.selected) {
                &.correct {
                    border-color: @correctGreen;
                    .checkboxIcon{
                        background-color: @correctGreen;
                        border-color: @correctGreen;
                        .svgWrap{
                            stroke: #fff;
                        }
                    }
                }
            }
            
            &.selected {
                
                &:not(.correct) {
                    background-color: @incorrectRed !important;
                    border-color: @incorrectRed;
                    .checkboxIcon{
                        background-color: #fff;
                        border-color: #fff;
                        .svgWrap{
                            stroke: @incorrectRed;
                        }
                    }
                    .btnText{
                        color: #fff;
                    }
                }
                &.correct {
                    background-color: @correctGreen !important;
                    border-color: @correctGreen;
                    .checkboxIcon{
                        background-color: #fff;
                        border-color: #fff;
                        
                        .svgWrap{
                            stroke: @correctGreen
                        }
                        // .svg-tickcross{
                        //     svg {
                        //         stroke: #111;
                        //     }
                        // }
                    }
                    .btnText{
                        color: #fff;
                    }
                }
            }
        }
        
        &:focus{
            .btnFocus;
        }
        
        .checkboxIcon{
            height: 22px;
            width: 22px;
            padding: 3px;
            position: absolute;
            // background-color: @greyDark;
            background-color: transparent;
            border: 1px solid @grey;
            top: 50%;
            margin-top: -11px;
            
            .svgWrap{
                stroke: @white;
//                display: none;
            }
        }
        .optionInner{
            padding: 20px;
            // background-color: @greyLight;
            .transition();
            .optionImg{
                margin-bottom: 5px;
                img{
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
        .btnText{
            margin-left: 42px;
            margin-right: 25px;
        }
        .tickCross{
//            position: absolute;
//            top: 2px;
//            right: 2px;
            width: auto !important;
            padding: 1px;
            .svgWrap{
                stroke-width: 10px;
            }
        }
        &.selected{
            .checkboxIcon{
                // .applyModuleStyling(background-color);
                background-color: @greyMid;
            }
        }
        &.attempted{
            opacity: .5;
        }
        .option-compare-selection {
            text-align: center;
            border-top: 2px solid @greyDark;
            // margin-top: -5px;
            margin-bottom: -5px;
            display: none;
            .clearfix;
            >div{
                float: right;
                background-color: @greyDark;
                border-radius: 0 0 5px 5px;
                padding: 0px 15px;
                margin-left: 10px;
                margin-right: 10px;
                // display: inline-block;
                font-size: 0.9em;
                font-style: italic;
                color: @white;
            }
        }
        &.previouslySelected{
        }
    }
    &:not(.done){
        .option{
            
            &:not(.disabled){
                &:hover {
                    .optionInner{
                        background-color: @optionHoverBgColor;  
                    }
                }
                &:hover,
                &:focus{
                    .optionInner{
                        .checkboxIcon{
                            background-color: #fff;
                        }
                        
                    }
                    //.scale(1.01);
                }
                &.selected{
                    .checkboxIcon{
                        // .applyModuleStyling(background-color);
                        background-color: @greyDark !important;
                        border-color: @greyDark;
                    }
                }
            }
        }
    }
    // .mcqtawa-btn{
    //     padding: 8px 24px;
    //     &.disabled{
    //         background-color: transparent !important;
    //         border: 1px solid @greyMid;
    //         color: @greyMid !important;
    //         opacity: 0.4;
    //     }
    //     &:not(.disabled){
    //         border: 1px solid transparent;
    //     }
    // }
}
