table{
    &.table{
        .modal &{
            font-size: .8em;
        }
        thead{
            tr{
                th{
                    padding:2px;
                    .applyModuleStyling(border-bottom-color);
                }
            }
        }
        tbody{
            td {
                padding:2px;
                ul{
                    padding-left: 13px;
                    margin-bottom: 0px;
                }
                .table-tick{
                    width: 30px;
                    height: 30px;
                    margin: 0 auto;
                    .svgWrap{
                         .applyModuleStyling(stroke);
                        stroke-width: 6;
                    }
                }
            }
        }
    }
    .table-responsive &{
        @media (max-width: 767px){
            max-width: none;
            th, td {
                white-space: normal !important;
            }
        }
    }
}

.table{
    width: 100%;
    display: table;
    &:not(table){
        margin-bottom: 0;
    }
    .tablerow{
        display: table-row;
    }
    .tablecell{
        display: table-cell;
        vertical-align: middle;
        float: none; // override bootstrap float if necessary
    }
    .tablecelltop{
        display: table-cell;
        vertical-align: top;
    }
}

.larger{
    font-size: 1.2em;
}
.smaller{
    font-size: .9em;
}

ul{
    &.reduceIndent{
        .ltr &{
            padding-left: 15px;
        }
        .rtl &{
            padding-right: 15px;
        }
    }
    &.noIndent{
        .ltr &{
            padding-left: 0px;
        }
        .rtl &{
            padding-right: 10px;
        }    
    }
    &.noBullets{
        list-style: none;
    }
}

ol{
    li{
        &::marker{
            color: @hsbcRed;
        }
    }
}

#content &,
.modal &{
    a{
        &[data-popover]{
            font-weight: bold;
            padding: 3px 5px;
            text-decoration: none;
            // border: 1px solid;
            display: inline-block;
            background-color: @color1;
            color: @white;
            &:hover{                
                background-color: @color1Light;
            }
        }

    }
}

.btn{
    font-size: @bodyFontSize;
    font-weight: bold;
    //.font1Light();
    line-height: inherit;
    border-radius: 0px;
    white-space: normal;
    border: none;
    &:not(.toggle){
        //text-align: left;
        color: @buttonColor;
        background-color: @buttonBkgColor;
        padding: 8px 10px;
        min-width: 80px;
        
        min-height: 40px;
        .transition();
        &:hover,
        &:focus{
            color: @buttonColor;
            // outline: none;
        }
        &:not(.disabled){
            cursor: pointer;
            &:hover,
            &:focus{
                color: @buttonColor;
                background-color: @buttonHoverBkgColor;
                outline-offset: 2px;
            }
        }
    }
    &.disabled{
        opacity: 1; // can't use with accessibility
        // border: 1px solid @buttonDisabledColor;
        color: darken(@greyMid, 5%) !important;
        background-color: @greyLight !important;
        .bkgColorLightGrey &{
            background-color: @white !important;
        }
    }
//    &.btn-absolute{
//        position: absolute;
//        left: 5px;
//    }
    &.btn-inline{
        .ltr &{
            margin-right: 5px;
        }
        .rtl &{
            margin-left: 5px;
        }
    }
    &.btn-inverse{
        color: @buttonInverseBkgColor;
        background-color: @buttonInverseColor;
        &:hover,
        &:focus{
            color: @buttonInverseBkgColor;
        }
        &:not(.disabled){
            &:hover,
            &:focus{
                color: @buttonInverseBkgColor;
                background-color: tint(@buttonInverseBkgColor,75%);
            }
        }
    }
    &.ok{
        text-align: center;
    }
    &.continueBtn{
        position: relative;
        // min-width: auto;
        min-height: 44px;
//        height: 40px;
        
        .ltr &{
            padding-right: 0px;
            margin-right: 20px;
            &:before{
                border-left: 22px solid;
                right: -22px;
            }
        }
        .rtl &{
            padding-left: 0px;
            margin-left: 20px;
            &:before{
                border-right: 22px solid;
                left: -22px;
            }
        }
        &:before{
            border-top: 22px solid transparent;
            border-bottom: 22px solid transparent;
            content: "";
            position: absolute;
            top: 0px;
            .transition();
        }
    }
    &.skipBtn{
        .ltr &{
            margin-right: 5px;
        }
        .rtl &{
            margin-left: 5px;
        }
    }
    
    &.btn-image{
        max-width: 100%;
        img{
            margin: 0 auto;
            margin-bottom: 5px;
        }
        &.btn-nolabel{
            margin-bottom: 0px;
        }
    }
    &.btn-svg{
        .svgIcon{
            max-width: 50px;
            margin: 0 auto;
        }
        &.btn-icon{
            .svgIcon{
                float: left;
            }
        }
        .svgWrap {
            stroke-width: 4px;
        }
    }
    &.btn-sequence{
        &.btn-inline{
            .btnText{
                font-size: 2em;
            }
            &:not(.btn-sequence-last){
                .ltr &{
                    &:before{
                        border-left: 10px solid;
                        right: -10px;
                    }
                }
                .rtl &{
                    &:before{
                        border-right: 10px solid;
                        left: -10px;
                    }
                }
                &:before{
                    content: "";
                    border-top: 10px solid transparent;
                    border-bottom: 10px solid transparent;
                    position: absolute;
                    top: 50%;
                    margin-top: -10px;
                }
            }
        }
        &.btn-block{
            &:not(.btn-sequence-last){
                &:before{
                    content: "";
                    position: absolute;
                    left: 50%;
                    bottom: -20px;
                    margin-left: -10px;
                    border: 10px solid transparent;
                    border-top: 10px solid;
                }
            }
        }
    }
    &.hotspot{
        .svgWrap{
            stroke: #fff;
        }
    }
    &.btn-active-arrow-right{
        width: auto;
        .ltr &{
            margin-right: 10px;
            &:before{
                border-left: 10px solid;
                right: -10px;
            }
        }
        .rtl &{
            margin-left: 10px;
            &:before{
                border-right: 10px solid;
                left: -10px;
            }
        }
        &:before{
            content: "";
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            position: absolute;
            top: 50%;
            margin-top: -10px;
        }
    }
    &.btn-active-arrow-bottom{
        width: auto;
        &:before{
            content: "";
            border: 10px solid transparent;
            position: absolute;
            bottom: -20px;
            left: 50%;
            border-top: 10px solid;
            margin-left: -10px;
        }
    }
    
    &.btn-icon,
    // &.btn-svg,
    &.certificateBtn,
    &.infoBtn,
    &.resourceBtn,
    &.courseFeedbackBtn,
    &.emailBtn{
        text-align: left;
        .ltr &{
            padding-left: 35px;
        }
        .rtl &{
            padding-right: 35px;
        }
    }

    &.btn-icon{
        .btnImage{
            width: 30px;
            height: 30px;
            position: absolute;
            top: 4px;
            .ltr &{
                left: 3px;
            }
            .rtl &{
                right: 3px;
            }
        }
    //}
    //&.btn-svg{
        .svgIcon{
            width: 30px;
            height: 30px;
            position: absolute;
            top: 4px;
            .ltr &{
                left: 3px;
            }
            .rtl &{
                right: 3px;
            }
        }
    }

    &.certificateBtn,
    &.infoBtn,
    &.resourceBtn,
    &.courseFeedbackBtn,
    &.emailBtn{
        &:after{
            content:'';
            background-repeat: no-repeat;
            background-size: contain;
            width: 30px;
            height: 30px;
            position: absolute;
            top: 4px;
            .ltr &{
                left: 3px;
            }
            .rtl &{
                right: 3px;
            }
        }
    }
    
    &.certificateBtn{
        &:after{
            background-image: url('../UI/images/icons/icon-certificate.png');
        }
    }
    &.courseFeedbackBtn{
        &:after{
            background-image: url('../UI/images/icons/icon-feedback.png');
        }
    }
    &.resourceBtn{
        &:after{
            background-image: url('../UI/images/icons/icon-resource.png');
            background-size: 100% 600%;
        }
        &.moduleBtnInvert{
            &:after{
                background-position: 0 20%;
            }
        }
    }
    &.infoBtn{
        &:after{
            background-image: url('../UI/images/icons/icon-info.png');
        }
    }
    &.emailBtn{
        &:after{
            background-image: url('../UI/images/icons/icon-email.png');
        }
    }

    &.backBtn{
        position: relative;
        background-color: transparent;
        padding: 0;
        min-width: auto;
        min-height: auto;
        margin-bottom: 30px;
        .help &{
            margin-bottom: 0px;
        }
        &:before{
            border-top:6px solid transparent;
            border-bottom:6px solid transparent;
            content: "";
            position: absolute;
            top: 0.5em;
            .transition();
        }
        .ltr &{
            padding-left: 0px;
            margin-left: 15px;
            &:before{
                border-right:8px solid;
                left: -13px;
            }
            &:not(.disabled){
                &:hover{
                    &:before{
                        left:-16px;
                    }
                }
            }
        }
        .rtl &{
            padding-right: 0px;
            margin-right: 15px;
            &:before{
                border-left:8px solid;
                right: -13px;
            }
            &:not(.disabled){
                &:hover{
                    &:before{
                        right:-16px;
                    }
                }
            }
        }
    }
    
    &.exitBtn{
        .ltr &{
            padding-left: 45px;
        }
        .rtl &{
            padding-right: 45px;
        }
        &:before{
            content:'';
            background-image: url('../UI/images/icons/icon-exit.png');
            background-repeat: no-repeat;
            background-size: contain;
            width: 29px;
            height: 29px;
            position: absolute;
            top: 4px;
            .ltr &{
                left: 7px;
            }
            .rtl &{
                right: 7px;
            }
        }
    }

    &.toggle{
        color: @bodyColor !important;
        background-color: transparent !important;
        position: relative;
        padding: 5px;
        padding-left: 55px;
        &:not(.disabled){
            cursor: pointer;
            &:hover,
            &:focus{
                color: @bodyColor !important;
                background-color: transparent !important;
            }
        }
        .toggle-switch{
            width: 50px;
            height: 30px;
            //border: 1px solid @buttonBkgColor;
            background-color: @buttonBkgColor;
            border-radius: 15px;
            padding: 3px;
            position: absolute;
            top: 0px;
            left: 0;
            .transition();
            &:before{
                content: '';
                display: block;
                width: 24px;
                height: 24px;
                background-color: @white;
                border-radius: 12px;
                //left: 2px;
                .transition();
            }
        }
        &.on{
            .toggle-switch{
                background-color: @correctGreen;
                &:before{
                    margin-left: 20px;
                }
            }
        }
    }
}

