    .slideMetaID{
        display: none;
        .slidemapping &{
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            background-color: #0f0;
            padding: 0 2px;
        }
    }
    
    .section{
        padding-top: 50px;
        padding-bottom: 50px;
        background-size: cover;
        background-position: center;
        // mobile
        @media (max-width: 767px){
            padding-top: 50px;
            padding-bottom: 50px;
        }
        &.padColumns,
        &.p-col{
            padding-top: 0;
            padding-bottom: 0;
            > .container,
            > .container-fluid{
                > .row > div{
                    padding-top: 50px;
                    padding-bottom: 50px - @componentBottomMargin;
                }
            }
        }
        &.pt-0.pb-0{
            > .container,
            > .container-fluid{
                > .row > div{
                    &.p-col{
                        padding-top: 50px;
                        padding-bottom: 50px - @componentBottomMargin;
                    }
                }
            }
        }
    }
}

.noPadding{padding: 0  !important;}
.noTopPadding{padding-top: 0 !important;}
.noBottomPadding{padding-bottom: 0 !important;}

.noMargin{margin: 0  !important;}
.noTopMargin{margin-top: 0 !important;}
.noBottomMargin{margin-bottom: 0 !important;}

.hideOverflow{
    overflow: hidden;
}


.tickBox,
.tickCross{
    width: 22px;
    height: 22px;
    display: none;
}

// display tick
.done:not(.hideOptionAnswers) .correct,                         // not selected
.done:not(.hideOptionMarking) .selected.disabled.correct,       // user selected
.attempted:not(.hideOptionMarking) .selected.disabled.correct,
.done.selected.correct{
    .tickCross{
        display: block;
        .svg-tickcross {
            .cross{
                display: none;
            }
        }
    }    
}
// display cross
.done:not(.hideOptionMarking) .incorrect,                       // show crosses on non-selected incorrect
.done:not(.hideOptionMarking) .selected.disabled:not(.correct), // dont show crosses on non-selected incorrect
.attempted:not(.hideOptionMarking) .selected.disabled:not(.correct),
.done.selected:not(.correct){
    
    .tickCross{
        display: block;
        .svg-tickcross {
            display:block;
            .tick{
                display: none;
            }
        }
    }
}


