.popupContainer{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    .blank{
        width: 100%;
        height: 100%;
        background-color: rgba(255,255,255,.3);
    }
    .popupWrapper{
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        display: table;
        .popupHolder{
            display: table-cell;
            vertical-align: middle;
            width: 100%;
            .popup{
                position: relative;
                padding:10px;
                width: 660px;
                background-color: @popupBkgColor;
                color: @popupColor;
                margin: 0 auto;
                box-sizing: border-box;
                .popupClose{
                    position: absolute;
                    top:0;
                    right: 5px;
                    cursor: pointer;
                    font-size: 14px;
                    .btn_text{
                        float: left;
                        margin-top: 2px;
                    }
                    .btn_icon{
                        float: right;
                        display: block;
                        background-image: url('../UI/images/icon-navigation.png');
                        width: 10px;
                        height: 10px;
                        background-size: 96px 48px;
                        background-position: -79px -7px;
                        margin-top: 5px;
                        margin-left: 5px;
                    }
                }
                .popupTitle{
                }
                .popupBody{
                    
                }
            }
        }
    }
}

