.feedback{
    display: none;
    background-color: @feedbackBkgColor;

    &:not(.section){
        position: relative;
        padding:20px;
        margin-bottom: 10px;
        .feedback-flex-content{
            display: flex;
        }
        .feedback-icon{
            // position: absolute;
            width: 80px;
            .ltr &{
                margin-right: 10px;
            }
            .rtl &{
                margin-left: 10px;
            }
        }
        .feedback-text{
            // .ltr &{
            //     margin-left: 120px;
            // }
            // .rtl &{
            //     margin-right: 120px;
            // }
        }
        &.incorrect,
        &.partlycorrect{
            background-color: lighten(@incorrectRed, 50%);
        }
        &.correct{
            background-color: lighten(@correctGreen, 50%);
        }
    }
    .svg-tickcross{       
        .svgWrap{
            stroke: @incorrectRed;
            stroke-width: 4px;
        }
    }
    
    .feedbackCorrect,
    .feedbackPartlyCorrect,
    .feedbackIncorrect{
        
        // color: @@color;
    }
    .feedbackOption0,
    .feedbackOption1,
    .feedbackOption2,
    .feedbackOption3,
    .feedbackOption4,
    .feedbackOption5,
    .feedbackOption6,
    .feedbackOption8,
    .feedbackOption9,
    .feedbackOption10,
    .feedback1,
    .feedback2,
    .feedback3,
    .feedback4,
    .feedbackCompareCorrectCorrect,
    .feedbackCompareCorrectIncorrect,
    .feedbackCompareIncorrectCorrect,
    .feedbackCompareIncorrectIncorrect{
        display: none;
    }  

    .feedbackHeading{
        .h4;
    }
    
    .feedbackRetry,
    .feedbackDone{
        display: none;
    }
    
    &.correct{
        .feedbackIncorrect,
        .feedbackPartlyCorrect{
            &:not(.feedbackCorrect){
                display: none;
            }
        }  
        .feedbackCorrect{

        }
        .svg-tickcross{
            .svgWrap{
                stroke: @correctGreen;
                .cross{
                    display: none;
                }
            }
        }
    }
    &.incorrect{
        .feedbackCorrect,
        .feedbackPartlyCorrect{
            &:not(.feedbackIncorrect){
                display: none;
            }
        } 
        .svg-tickcross{
            .tick{
                display: none;
            }
        }    
    }
    &.partlycorrect{
        .feedbackCorrect,
        .feedbackIncorrect{
            &:not(.feedbackPartlyCorrect){
                display: none;
            }
        } 
        .svg-tickcross{
            .tick{
                display: none;
            }
        }    
    }
}



