.equalHeightCol{
    @media(min-width: 768px){
        display: table-cell;
        float: none;
        vertical-align: top;
    }
}

@media(min-width: 768px){
    .columns2{
        columns: auto 2;
        column-gap: 40px;
    }
    .columns3{
        columns: auto 3;
        column-gap: 40px;
    }
    .columns2,
    .columns3{
        >*:not(ol):not(ul),
        > ol > li,
        > ul > li{
            // display: table; // required for firefox IE not breaking correctly with break-inside: avoid
            page-break-inside: avoid;
            break-inside: avoid;
        }
    }
}