// main: ../../less/styles.less

.component-parallaxheader{

    @parallaxheaderHeight: 85vh;
    @parallaxheaderMinHeight: 500px;
    
    display: flex;
    // flex-direction: column;
    flex-wrap: wrap;
    // min-height: @parallaxheaderHeight;
    width: 100%;
    // overflow: hidden;
    
    // .fullscreen&{
    //     height: @viewportVisibleHeight;
    // }
    .buildup-container{
        overflow: hidden;
        
        width: 100%;
        @media(min-width: @minSM){
            height: @parallaxheaderHeight;
            min-height: @parallaxheaderMinHeight;
            .contain-image&{
                height: auto;
            }
        }

    }
    .buildup-layer-wrap{
        position: relative;
        width: 100%;
        // .fullscreen&{
            height: 100%;
        // }
        // @media(max-width: @maxXS){
        //     &:before{
        //         content: '';
        //         display: block;
        //         padding-bottom: 56.25%;
        //     }
        // }
        @media(min-width: @minSM){
            height: 100%;
        }
        
        .buildup-layer-temp-image{
            position: relative;
            visibility: hidden;
            // max-width: 100%;
            // .fullscreen&{
            @media(max-width: @maxXS){
                max-width: 100%;
            }
            @media(min-width: @minSM){
                max-width: none;
                height: @parallaxheaderHeight;
                min-height: @parallaxheaderMinHeight;
            }
            .contain-image&{
                max-width: 100%;
                height: auto;
            }
            // }
        }

        .buildup-image-layer{
            // .scale(1.1);
            // .ltr &{
            //     .scale(1.1);
            // }
            // .rtl &{
            //     .scale(-1.1, 1.1);
            // }
        }
        .buildup-content-layer{
            @media(min-width: @minSM){
                display: flex;
                align-items: center;
                max-height: @parallaxheaderHeight;
            }
        }
        .buildup-layer{
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            .img-top&{
                background-position: top center;
            }
            .img-bottom&{
                background-position: bottom center;
            }
        }
    }
    
    // align-items: flex-end;
    // .section{
        
    // }
    
    // .applyModuleStyling(background-color);

    &.hasImage{
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        // @media(min-width: 768px){
        //     height: 40vh;
        // }
        
        &:not(.fullscreen){
            align-items: flex-end;
        }
    }
    
    
    
    // margin-bottom: 0 !important;

    .overlay{
        width: 100%;
        @media(max-width: @maxXS){
            margin-top: -20px;
        }
        &.title-overlay{
            position: absolute;
            z-index: 10000;
            // align-self: start;
            .overlayContent{
                display: inline-block;
                padding: 10px 20px;
                margin: 0 -10px;
                color: #fff;
                .applyModuleStyling(background-color);
                
                @media(max-width: @maxXS){
                    margin-left: 20px;
                    margin-right: 20px;
                }

                // .fullscreen&{
                //     // height: @viewportVisibleHeight;
                //     margin-top: 15vh;
                // }
            }
        }
        &.content-overlay{
            @media(min-width: @minSM){
                position: absolute;
                bottom: -2em;
            }
            
            
            .overlayContent{
                display: inline-block;
                padding: 20px;
                margin: 0 -10px;
                color: #fff;
                
                animation-delay: 2s;
                // .applyModuleStyling(background-color);
                // background-color: fade(lighten(#0460A9, 60%), 90%);
                // background-color: #fff;
                // .fullscreen&{
                //     margin-bottom: 15vh;                    
                // }
            }
        }
    }

    .parallaxheader-layer-title{
        .text-gradient;
        text-align: right;
        font-size: 1.8em;
        // border: 1px solid #f00;
        @media(min-width: @minSM){
            font-size: 5em;
        }
    }

    .header-btn-wrap{
        position: absolute;
        width: 100%;
        max-height: calc(100vh - 100px);
        height: 100%;
        .header-btn{
            position: absolute;
            width: 40px;
            
            min-width: auto;
            min-height: auto;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            background-color: @primary1;
            cursor: pointer;
            padding: 15px 10px;
            &:focus{
                .btnFocus;
            }
            .svgWrap{
                stroke-width: 8px;
            }
        }
    }

}