.transition-delay(@delay){
    -webkit-transition-delay: @delay;
    -moz-transition-delay: @delay;
    -o-transition-delay: @delay;
    transition-delay: @delay;
}
.inner-shadow(@horizontal:0, @vertical:1px, @blur:2px, @alpha: 0.4) {
  -webkit-box-shadow: inset @horizontal @vertical @blur rgba(0, 0, 0, @alpha);
  -moz-box-shadow: inset @horizontal @vertical @blur rgba(0, 0, 0, @alpha);
  box-shadow: inset @horizontal @vertical @blur rgba(0, 0, 0, @alpha);
}
.box-shadow(@arguments) {
  -webkit-box-shadow: @arguments;
  -moz-box-shadow: @arguments;
  box-shadow: @arguments;
}
.box-sizing(@sizing: border-box) {
  -ms-box-sizing: @sizing;
  -moz-box-sizing: @sizing;
  -webkit-box-sizing: @sizing;
  box-sizing: @sizing;
}
.user-select(@argument: none) {
  -webkit-user-select: @argument;
  -moz-user-select: @argument;
  -ms-user-select: @argument;
  user-select: @argument;
}
.columns(@colwidth: 250px, @colcount: 0, @colgap: 50px, @columnRuleColor: #EEE, @columnRuleStyle: solid, @columnRuleWidth: 1px) {
  -moz-column-width: @colwidth;
  -moz-column-count: @colcount;
  -moz-column-gap: @colgap;
  -moz-column-rule-color: @columnRuleColor;
  -moz-column-rule-style: @columnRuleStyle;
  -moz-column-rule-width: @columnRuleWidth;
  -webkit-column-width: @colwidth;
  -webkit-column-count: @colcount;
  -webkit-column-gap: @colgap;
  -webkit-column-rule-color: @columnRuleColor;
  -webkit-column-rule-style: @columnRuleStyle;
  -webkit-column-rule-width: @columnRuleWidth;
  column-width: @colwidth;
  column-count: @colcount;
  column-gap: @colgap;
  column-rule-color: @columnRuleColor;
  column-rule-style: @columnRuleStyle;
  column-rule-width: @columnRuleWidth;
}
.translate(@x:0, @y:0) {
  .transform(translate(@x, @y));
}
.background-clip(@argument: padding-box) {
  -moz-background-clip: @argument;
  -webkit-background-clip: @argument;
  background-clip: @argument;
}
.text-shadow (@string: 0 1px 10px rgba(88, 89, 91, .8)) {
	text-shadow: @string;
}

.animation(@arguments){
    -webkit-animation: @arguments;
       -moz-animation: @arguments;
         -o-animation: @arguments;
            animation: @arguments;
}
.animation-delay(@arguments){
    -webkit-animation-delay: @arguments;
       -moz-animation-delay: @arguments;
         -o-animation-delay: @arguments;
            animation-delay: @arguments;
}

.media-min(@minBreakPoint, @rules){
    @media(min-width:@minBreakPoint) {
        @rules();
    }
}
.media-max(@maxBreakPoint, @rules){
    @media(max-width:@maxBreakPoint) {
        @rules();
    }
}
.media-min-max(@minBreakPoint, @maxBreakPoint, @rules){
    @media(min-width:@minBreakPoint) and (max-width:@maxBreakPoint) {
        @rules();
    }
}

/**
@baseFontSize: 10px; // how many px pre 1rem
html {
  font-size: @baseFontSize;
}
div {
  font-size:@baseFontSize;
}
*/
// https://codepen.io/Ariex/pen/YZNQVB

// This CSSLock will generate necessary media query on given breakpoints, to change font size smoothly between break points.
//
// @minSize: minimal size of font size, in em
// @minBreakPoint: when font reach its minimum size, in px
// @maxSize: maximal size of font size, in em
// @maxBreakPoint: when font reach its maxmium size, in px
// @baseFontSize: font size in px of the container of current element
.CSSLock(@minSize; @minBreakPoint; @maxSize; @maxBreakPoint; @baseFontSize: 16px) {
    // behind the scene:
    // y = ax + b
    // y is font-size we want (changes when viewport width resizes in certain range)
    // x is view port width (changes between from minBreakPoint to maxBreakPoint)
    // a is the font size changing rate per 1px screen width change
    //   given x = @minBreakPoint, y = @minSize and x = @maxBreakPoint, y = @maxSize
    //   a = (@maxSize - @minSize) / (@maxBreakPoint - @minBreakPoint)
    // b is base value where changes apply to. At here it is calculated by set x = @minBreakPoint and y = @minSize
    //   given b = y - ax
    //   when x = @minBreakPoint, y = @minSize
    //   we get b = minimalFontSize(in px) - ratio * @minBreakPoint

    @fontSizeOffset: (unit(@maxSize) - unit(@minSize)) * unit(@baseFontSize);
    @vwOffset: unit(@maxBreakPoint) - unit(@minBreakPoint);

    @a: round(unit(@fontSizeOffset) / unit(@vwOffset), 4); /* IE11 have a bug, the calculation will wrong when more than 10 decimals */
    
    @_minSize: unit(@minSize); // this value is in em
    @_baseFontSize: unit(@baseFontSize);
    @_minBreakPoint: unit(@minBreakPoint);
    
    @b: ~"@{_minSize} * @{_baseFontSize} * 1px - @{a} * @{_minBreakPoint} * 1px";
    
    // setup font size changing function, font size will gradually changed between minimal value and maximal value based on view port width
    @media(min-width:@minBreakPoint) and (max-width:@maxBreakPoint) {
        font-size: ~"calc(@{a} * 100vw + @{b})";
    }
    // setup the minimal font size break point
    @media(max-width:@minBreakPoint) {
        font-size: @minSize;
    }
}

.clearfix{
    zoom: 1;
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

//https://codepen.io/zvuc/pen/xvoys
.keyframes(@name; @arguments) {
  @-moz-keyframes @name { @arguments(); }
  @-webkit-keyframes @name { @arguments(); }
  @keyframes @name { @arguments(); }
}
.dark-panel-bg {
    background-color: #222324;
}

.aspect-ratio(@heightPercentage) {
	width: 100%;
	position: relative;
	> * {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
	&::before {
		content: "";
		display: block;
		padding-top: @heightPercentage;
	}
}

.imageBgCenterCoverNoRepeat(){
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center;
}

.scale(@x: 1.5, @y: @x){
  .transform(scale(@x, @y));
}