// main: ../../less/styles.less

.component-callout,
.callout{
	position: relative;
    padding: 20px;
//    border-radius: 4px;
    &:not(.callout-outline){
        background-color: @color1;
        color: #fff;
    }
    &.callout-outline{
        display: inline-block;
        &.hasCarousel{
            display: block;
        }
        // border-right: 2px solid @grey;
        // &::after {
        //     content: "";
        //     width: 0px;
        //     height: 0px;
        //     border: 8px solid transparent;
        //     position: absolute;
        // }
        &.left,
        &.right{
            // margin-left: 10px;
            // margin-right: 10px;
        }
        &.left{
            border-right: 2px solid @color1;
            // &:after  {
            //     right: -16px;
            //     top: 50%;
            //     margin-top: -8px;
            //     border-left: 8px solid @white;
            // }
        }
        &.right{
            border-left: 2px solid @color1;
            // &:after  {
            //     left: -16px;
            //     top: 50%;
            //     margin-top: -8px;
            //     border-right: 8px solid @white;
            // }
        }
        
        &.top{
            border-bottom: 2px solid @color1;
            // &:after{
            //     bottom: -16px;
            //     left: 50%;
            //     margin-left: -8px;
            //     border-top: 8px solid @white;
            // }
        }
        &.bottom{
            border-top: 2px solid @color1;
            // &:after{
            //     top: -16px;
            //     left: 50%;
            //     margin-left: -8px;
            //     border-bottom: 8px solid @white;
            // }
        }
    }
    &::before {
        content: "";
        width: 0px;
        height: 0px;
        border: 10px solid transparent;
        position: absolute;
    }

    &.top::before {
        left: 50%;
        bottom: -20px;
        margin-left: -10px;
        border-top: 10px solid @color1;
    }

    &.bottom::before {
        left: 50%;
        margin-left: -10px;
        top: -20px;
        border-bottom: 10px solid @color1;
    }

    &.left::before {
        right: -20px;
        top: 50%;
        margin-top: -10px;
        border-left: 10px solid @color1;
    }

    &.right::before {
        left: -20px;
        top: 50%;
        margin-top: -10px;
        border-right: 10px solid @color1;
    }

    &.top-left::before {
        left: 40px;
        bottom: -20px;
        border-top: 10px solid @color1;
    }

    &.top-right::before {
        right: 40px;
        bottom: -20px;
        border-top: 10px solid @color1;
    }
    &.bottom-left{
        &:before {
            left: 40px;
            top: -20px;
            border-bottom: 10px solid @color1;
        }
        &.btn-inline-callout-0::before{left: 20px;}
        &.btn-inline-callout-1::before{left: 85px;}
        &.btn-inline-callout-2::before{left: 150px;}
        &.btn-inline-callout-3::before{left: 215px;}
        &.btn-inline-callout-4::before{left: 280px;}
    }
    
//        &.bottom-left20::before {
//            left: 20px;
//            top: -20px;
//            border-bottom: 10px solid @color1;
//        }
    &.bottom-right{
        &:before {
            right: 40px;
            top: -20px;
            border-bottom: 10px solid @color1;
        }
        &.btn-inline-callout-0::before{right: 20px;}
        &.btn-inline-callout-1::before{right: 85px;}
        &.btn-inline-callout-2::before{right: 150px;}
        &.btn-inline-callout-3::before{right: 215px;}
        &.btn-inline-callout-4::before{right: 280px;}
    }
//        &.bottom-right20::before {
//            right: 20px;
//            top: -20px;
//            border-bottom: 10px solid @color1;
//        }
    &.left-top::before {
        right: -20px;
        top: 5px;
        border-left: 10px solid @color1;
        border-top: 0;
    }

    &.right-top::before {
        left: -20px;
        top: 5px;
        border-right: 10px solid @color1;
        border-top: 0;
    }
    
    &.top,
    &.top-right,
    &.top-left{
        margin-bottom: 20px;
    }
//    .callout-inner{
//        &:before{
//            position: absolute;
//            content: '"';
//            top: 0;
//            left: 0;
//            font-size: 2em;                
//        }
//    }
    // h4{
    //     font-size: 1.8em;
    //     color: #fff;
    //     &:last-child{
    //         margin-bottom: 0;
    //     }
    // }
    &.indentRight{
        margin-right: 50px !important;    
    }
    &.indentLeft{
        margin-left: 50px !important;    
    }

    
    
    .svgIcon{
        display: none;
    }
    &.callout-inline{
        display: inline-block;
        vertical-align: top;
        max-width: 80%;
    }
    &.callout-quotation,
    &.callout-quotation-small{
        padding: 0;
    }
    &.callout-question{
        padding: 0;
        .ltr &{
            margin-right: 10%;
        }
        .rtl &{
            margin-left: 10%;
        }
        /*padding-left: 50px;
        padding-right: 50px;
        //text-align: center;
        .callout-inner{
            &:before,
            &:after{
                position: absolute;
                font-size: 7em;
            }
            .ltr &{
                &:before{
                    content: '';
                    top: -0.2em;
                    left: 5px;
                }
                &:after{
                    content: '';
                    bottom: -0.8em;
                    right: 5px;
                }
            }
            
            .rtl &{
                &:before{
                    content: '';
                    bottom: -0.8em;
                    left: 5px;
                }
                &:after{
                    content: '';
                    top: -0.2em;
                    right: 5px;
                }
            }
        }*/
            
    }
    &.callout-reveal{
        .callout-color(@color1Dark);
        .ltr &{
            margin-left: 10%;
            float: right;
        }
        .rtl &{
            margin-right: 10%;
            float: left;
        }
        width: 80px;
//        .transition();
        .svgIcon{
            display: block;
//            max-width: 50px;
            .svgWrap{
                stroke-width: 4px;
            }
        }
        &.shown{
            float: none;
            width: auto;
        }
        .callout-inner{
            display: none;
        }
        &:not(.shown){
            &.top-left::before {
                left: 20px;
            }
            &.top-right::before {
                right: 20px;
            }
        }
        &.callout-lightgrey{
            color: @colorDark;
            .callout-color(@greyLight);
            .svgWrap{
                .applyModuleStyling(stroke);
            }
        }
    }
    &.action{
        .transition();
        &:before{
            .transition();
        }
    }

    .quotee {
        font-weight: bold;
        opacity: .6;
    }

    &.callout-white{
        color: @colorDark;
        .callout-color(@white);
    }
    &.callout-whiteTransparent{
        color: @colorDark;
        .callout-color(fade(@white,90%));
    }
    &.callout-grey{
        .callout-color(@grey);
    }
    &.callout-color1{
        .callout-color(@color1);
    }
    &.callout-lightgrey{
        color: @colorDark;
        .callout-color(@greyLight);
    }
    &.callout-midgrey{
        color: @colorDark;
        .callout-color(@grey);
    }
    &.callout-darkgrey{
        .callout-color(@greyDark);
    }
}