[tabindex="-1"] {
    outline: none;
}

//https://stackoverflow.com/questions/66947723/aria-hidden-true-elements-contain-focusable-descendents-best-way-to-fix-t
.bxslider{
    >*[aria-hidden=true] {
        a[href],
        area[href], 
        input:not([disabled]), 
        select:not([disabled]), 
        textarea:not([disabled]), 
        button:not([disabled]), 
        [tabindex]:not([disabled]), 
        [contenteditable=true]:not([disabled]){
            visibility: hidden;
        }
    }
}

.bx-wrapper{
    .bx-pager{
        padding-top: 0;
        .bx-pager-item{
            .bx-pager-link{
                background-color: @greyMid;
                &:not(.disabled){
                    &.active,
                    &:hover,
                    &:focus{
                        background-color: @color1;
                    }
                }
                &.disabled{
                    background-color: @grey;
                }
                &:focus{
                    .btnFocus;
                }
            }
        }
    }
}

fieldset{
    legend{
        margin-bottom: 0;
        font-size: 1em;
        border-bottom: none;
    }
}

a, button, div {
    // &:disabled{
    &[aria-disabled="true"]{
        &:focus {
            pointer-events: none;
        }
    }
}

.btn,
.nav-btn{
    
    &:focus{
        .btnFocus;
    }
}

.btnFocus(){
    outline: 2px solid @color1;
    outline-offset: 2px;

    .bkgColorHsbcRed &{
        outline: 1px solid @white;
    }

}

.ui-selectmenu-button{
    &.ui-state-focus{
        .btnFocus;
        // &:focus{
        // }
    }
}

.highcharts-figure{
    
    .highcharts-root{
        font-family: 'Arial', sans-serif !important;
    }
    
    .highcharts-background {
        fill: none;
    }
    .highcharts-axis-line{
        stroke: #ccc;
    }
    .highcharts-grid-line{
        stroke: #fff;
    }
    .highcharts-credits{
        display: none;
    }
    .highcharts-caption{
        text-align: center;
    }
}
                